/* ----- Commnad boxes ------------------- */
.command-box {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 48%;
  margin-left: 1%;
  margin-right: 1%;
  margin-bottom: 20px;
  &es {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%; }

  &__img {
    width: 130px;
    height: 130px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
    margin-bottom: 10px;
    >img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top; } }

  &__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    .btn {
      margin-top: auto;
      min-width: 120px; } }

  &__name {
    color: $cl-site-text;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.1;
    margin-bottom: 10px;
    letter-spacing: $letspacing5; }

  &__txt {
    color: #555;
    font-size: 12px;
    font-weight: 300;
    line-height: 16px;
    letter-spacing: $letspacing-standart;
    margin-bottom: 10px; } }

.img-inner-border_inner {
  position: relative;
  &:before {
    content: ' ';
    position: absolute;
    top: 10px;
    bottom: 10px;
    right: 10px;
    left: 10px;
    border: 1px solid rgba(230, 230, 230, 0.5); } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .command-box {
    text-align: center;
    display: flex;
    flex-direction: column;
    width: 31.3%;
    margin-left: 1%;
    margin-right: 1%;
    margin-bottom: 20px;
    &es {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin-left: -1%;
      margin-right: -1%; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .command-box {
    max-width: 350px;
    padding-left: 10px;
    padding-right: 10px;
    margin-left: 0;
    margin-right: 0;
    &es {
      margin-left: -10px;
      margin-right: -10px; }
    &__name {
      color: $cl-site-text;
      font-size: 18px;
      font-weight: 500;
      line-height: 1.1;
      margin-bottom: 10px;
      letter-spacing: $letspacing5; }

    &__txt {
      color: #555;
      font-size: 14px;
      font-weight: 300;
      line-height: 20px;
      letter-spacing: $letspacing-standart;
      margin-bottom: 20px; }

    &__img {
      width: 220px;
      height: 220px;
      margin-bottom: 30px; }

    &__content {
      .btn {
        min-width: 153px; } } }

  .img-inner-border_inner {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      top: 25px;
      bottom: 25px;
      right: 30px;
      left: 30px;
      border: 1px solid rgba(230, 230, 230, 0.5); } }
  /* end media min width 768 pixels */ }

/* ----- END Commnad boxes --------------- */
