/* ----- Product ------------------- */
.product-box {
  margin-bottom: 50px; }

.prod-img {
  max-width: 400px;
  margin: 0 auto 30px; }

.prod-btn {
  margin-bottom: 30px; }

.prod-price {
  color: $cl-site-hover;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: $letspacing10;
  margin-bottom: 30px; }

.prod-description {
  color: $cl-site-text;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 30px;
  a {
    letter-spacing: $letspacing5;
    color: $cl-site-hover;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
    margin-left: 5px;
    outline: none;
    white-space: nowrap;
    &:hover {
      text-decoration: none;
      color: darken($cl-site-hover, 10%); } } }

.slider-product-item {
  margin: 0 15px; }

.dots-wrap-zero {
  .slick-prev {
    left: -15px; }
  .slick-next {
    right: -15px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .product-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__left {
      flex-basis: 51%;
      max-width: 51%;
      flex-shrink: 0; }
    &__right {
      flex-basis: 47%;
      max-width: 47%; } }

  .prod-img {
    max-width: none; }

  .slider-product {
    padding: 0 50px;
    .slick-prev {
      left: 5px; }
    .slick-next {
      right: 5px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .product-box {
    margin-bottom: 77px;
    &__left {
      flex-basis: 51%;
      max-width: 51%;
      flex-shrink: 0; }
    &__right {
      flex-basis: 44.5%;
      max-width: 44.5%; } }
  .prod-tt {
    margin-bottom: 24px; }
  .prod-price {
    margin-bottom: 43px; }
  .prod-img {
    margin-bottom: 30px; }

  .slider-product {
    padding: 0 50px;
    .slick-prev {
      left: 0; }
    .slick-next {
      right: 0; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-product {
    padding: 0 145px;
    .slick-prev,
    .slick-next {
      margin-top: -50px; }
    .slick-prev {
      left: 40px; }
    .slick-next {
      right: 40px; } }
  /* end media min width 1200 pixels */ }

/* ----- END Product --------------- */
