/* ----- Contacts ------------------- */
//map
.main-map {
  min-height: 300px;
  margin-left: -15px;
  margin-right: -15px;
  background-color: #ccc;
  outline: none; }

/* Google MAP hidden nav */
.gmnoprint, .gm-style-cc, .gm-style button {
  display: none !important; }

.contact-adr__inner {
  display: inline-block;
  text-align: center; }

.contact-adr__icon {
  margin-bottom: 20px; }

.contact-info__content {
  text-align: center; }

.contact-adr {
  &__sity {
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: $letspacing10;
    margin-bottom: 10px; }
  &__street {
    color: $cl-site-text;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    margin-bottom: 20px; } }

.contact-block {
  padding-bottom: 40px; }

.contact-info {
  .f-contact-box__inner {
    font-weight: 400;
    font-size: 14px; } }

.line-top {
  border-top: 1px solid $cl-silver; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .contact-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .contact-info__content {
      width: 30%; }
    .main-map {
      width: 65%;
      margin-left: 0;
      margin-right: 0;
      height: 393px;
      min-height: 393px; } }

  .contact-block {
    padding-bottom: 60px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .contact-block {
    max-width: 980px;
    margin: 0 auto;
    .f-contact-box__inner {
      padding-left: 40px; } }
  .contact-info .contact-info__content {
    width: 37%; }
  .contact-info .main-map {
    width: 62%; }
  .contact-adr__icon {
    margin-bottom: 18px; }
  .contact-adr__sity {
    margin-bottom: 8px; }
  .contact-adr__street {
    margin-bottom: 30px; }
  /* end media min width 1000 pixels */ }

/* ----- END Contacts --------------- */
