/* ----- table ------------------- */

table {
  width: 100%;
  border-collapse: collapse; }

.prod-table {
  margin-bottom: 30px;
  td {
    padding: 9px 10px 9px 0;
    color: $cl-site-text;
    font-size: 14px;
    line-height: 18px;
    vertical-align: top;
    letter-spacing: $letspacing9;
    border-bottom: 1px solid $cl-site-text-five;
    &:first-child {
      font-size: 12px;
      font-weight: 300;
      letter-spacing: $letspacing8;
      color: $cl-site-text; } } }


.table-desc {
  color: #252525;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.4px;
  padding: 0 15px;
  margin-bottom: 20px; }

.table-st {
  overflow: hidden;
  margin: 20px -15px;
  &__scroll {
    overflow: auto;
    table {
      width: 462px; } }
  table {
    margin-bottom: 20px; }
  thead {
    background-color: #ddd;
    background-image: linear-gradient(to right, #a07eaf 0%, #a07eaf 100%); }
  .th-tt-big {
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.6px; }
  .th-tt {
    font-size: 12px;
    font-weight: 500;
    line-height: 14px;
    letter-spacing: 0.5px; }
  th {
    color: #fff;
    font-size: 10px;
    line-height: 14px;
    letter-spacing: 0.4px;
    vertical-align: bottom;
    padding: 10px 5px;
    font-weight: 400;
    &:first-child {
      padding-left: 10px; }
    &:last-child {
      padding-right: 10px; }
    b {
      font-weight: 500;
      display: block;
      letter-spacing: 0.4px; } }

  td {
    border: 1px solid #bababa;
    border-top: none;
    padding: 10px 5px;
    text-align: center;
    color: #252525;
    font-size: 10px;
    line-height: 14px;
    letter-spacing: 0.4px;
    &:first-child {
      padding-left: 10px;
      text-align: left; }
    &:last-child {
      border-left: none;
      padding-right: 10px; } }

  .td-grid {
    text-align: left;
    &__img {
      display: none; } } }

.table-link {
  .btn {
    width: 100%;
    max-width: 412px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .table-st__scroll {
    overflow: hidden;
    table {
      width: 100%; } }

  .table-st_price-width-column {
    th, td {
      width: 75px;
      &:first-child {
        width: auto; } } }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .table-st {
    .td-grid {
      position: relative;
      padding: 0; }
    .td-grid__img {
      display: block;
      position: absolute;
      top: 50%;
      left: 15px;
      margin-top: -28px;
      border-radius: 4px;
      background-color: #000;
      width: 81px;
      height: 55px; }
    .td-grid__txt {
      min-height: 59px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      padding: 5px 5px 5px 95px; }
    td, th {
      &:first-child {
        padding-left: 15px; }
      &:last-child {
        padding-right: 15px; } } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .prod-table {
    td {
      &:first-child {
        width: 150px; } } }

  .table-st {
    margin-bottom: 40px;
    margin-top: 35px;
    .th-tt {
      font-size: 16px;
      line-height: 18px; }
    th {
      font-size: 14px;
      line-height: 18px; }

    td {
      font-size: 14px;
      line-height: 16px; } }

  .table-st_price-width-column {
    th, td {
      width: 95px;
      padding: 15px 5px 15px; } }

  .table-desc {
    font-size: 16px;
    line-height: 20px; }

  .table-st {
    .th-tt-big {
      font-size: 18px;
      line-height: 20px; } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .table-st {
    .th-tt-big {
      font-size: 24px;
      line-height: 28px; } }
  .table-st {
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    .th-tt {
      font-size: 20px;
      line-height: 28px; }
    th {
      font-size: 16px;
      line-height: 22px; }

    td {
      font-size: 16px;
      line-height: 20px;
      padding: 20px 10px;
      &:last-child {
        border-right-width: 2px; } }

    td, th {
      width: 105px; } }
  .table-desc {
    padding: 0 30px; }
  /* end media min width 1000 pixels */ }

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .prod-table td:first-child {
    width: 210px; }
  .table-link {
    margin-top: 55px; }
  .table-st table {
    margin-bottom: 29px; }
  /* end media min width 1200 pixels */ }

/* ----- END table --------------- */
