/* ----- About ------------------- */
.video-block {
  position: relative;
  outline: none;
  overflow: hidden;
  text-decoration: none;
  min-height: 200px;
  display: inline-block;
  border-radius: 4px;
  display: block;
  background-color: #ccc;
  z-index: 1;
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -35px;
    margin-left: -35px;
    background: rgba(#acacac, 0.8) url(../img/icons/play-st.svg) 28px center no-repeat;
    border-radius: 50%;
    width: 70px;
    height: 70px; }
  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 120%;
    z-index: -1; } }

a.video-block {
  img {
    transition: ease 0.1s; }
  &:hover {
    >img {
      transform: translate(-50%, -50%) scale(1.03); } } }

.about-line-bt {
  border-bottom: 1px solid $cl-silver; }

.about-inner {
  padding: 50px 0 20px; }

.about-grid_push {
  margin-bottom: 45px; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .about-grid {
    display: flex;
    justify-content: space-between;
    &__half-col {
      width: 48%; }
    &_correction.about-grid {
      .about-grid__half-col {
        width: 47%; } } }

  .video-block {
    min-height: 300px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .video-block {
    min-height: 400px; }
  .about-inner {
    padding: 70px 0 20px;
    &_sm {
      padding-top: 50px; } }
  .about-inner_md {
    padding-top: 90px; }
  .about-inner_md_second {
    padding-top: 80px; }
  .about-grid {
    &_correction.about-grid {
      .about-grid__half-col {
        &:first-child {
          width: 43%; }
        &:last-child {
          width: 51%; } } } }

  /* end media min width 1000 pixels */ }

/* ----- END About --------------- */
