/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  background-color: $btn-fill-color;
  background-image: $gradient-st;
  // box-shadow: $shadow3

  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: $btn-fill-hover-color;
    background-image: $gradient-st-hover; }


  &_second-st {
    background-color: $btn-fill-second-color;

    &:hover {
      background-color: $btn-fill-second-hover-color; } }

  &_border {
    border: $btn-border;
    color: $btn-border-color-text;
    line-height: $btn-border-line-height;
    background: transparent;
    &_dark {
      color: $cl-site-text-second; }

    &:hover {
      color: $btn-border-hover-color-text;
      background-color: $btn-border-hover-color;
      border-color: transparent;
      border-top: none;
      border-bottom: none;
      line-height: 24px; }
    &.btn_light {
      color: #fff; } }

  &_min-width {
    min-width: $btn-width-min;
    &_second {
      min-width: 170px; }
    &_three {
      min-width: 188px; } }

  &_max {
    width: 100%; }

  &_tini {
    font-weight: 300; }

  &_sm {
    padding: $btn-padding-sm;
    font-size: 14px;
    border-radius: 18px;
    &all {
      padding: 4px 15px;
      font-size: 14px;
      border-radius: 16px;
      &.btn_min-width {
        min-width: 173px; } }

    &_second {
      padding: 7px 30px; }

    &.btn_min-width {
      min-width: $btn-width-min-sm;
      &_second {
        min-width: 153px; }
      &_three {
        min-width: 170px; } }
    &_main {
      font-size: 14px;
      letter-spacing: 0.35px;
      color: #252525;
      padding-left: 15px;
      padding-right: 15px; } }

  &_md {
    padding: $btn-padding-md;
    border-radius: 26px;
    &_font-lg {
      font-size: 18px; }


    &.btn_min-width {
      min-width: $btn-width-min-md;
      &_second {
        min-width: 248px; }
      &_three {
        min-width: 230px; }
      &_big {
        min-width: 290px; }
      &_lg {
        min-width: 285px; }
      &_four {
        min-width: 220px; } } }

  &_lg {
    padding: $btn-padding-lg;
    border-radius: 30px;

    &.btn_min-width {
      min-width: $btn-width-min-lg; } }

  &__icon-text {
    display: inline-block;
    vertical-align: middle;
    font-size: 12px; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;
    &.icon-s_houzz {
      position: relative;
      top: -2px; }

    &_right {
      margin-left: 5px; }

    &_left {
      margin-right: 5px; } }

  &_long {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px; }

  &_ff-second {
    font-family: $site-font-family-2, Arial, sans-serif;
    letter-spacing: $letspacing7;
    color: #f9f9f9; }

  // &_font-bold
  //   font-family: $btn-font-family-bold
  //   font-weight: $btn-font-weight-bold

  // &_font-light
  //   font-family: $btn-font-family-light
  //   font-weight: $btn-font-weight-light

  // &_size-sm
  //   font-size: $btn-size-sm
  // &_size-md
  //   font-size: $btn-size-md
  // &_size-lg
  //   font-size: $btn-size-lg

  // &_up
  //   text-transform: uppercase

  // &_radius-sm
 }  //   border-radius: $btn-radius-sm

.l-back, .l-next {
  display: inline-block;
  position: relative;
  color: $cl-site-text;
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: $letspacing7;
  padding-left: 30px;
  text-decoration: none;
  outline: none;
  &:hover {
    color: $cl-site-text;
    text-decoration: underline; }
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    margin-top: -7px;
    left: 0;
    background: url(../img/icons/arrow-left-double.svg) center center no-repeat;
    width: 14px;
    height: 15px;
    transition: ease-in-out 0.1s; } }
.l-back {
  &:hover {
    &:before {
      left: -3px; } } }
.l-next {
  padding-left: 0;
  padding-right: 30px;
  text-align: right;
  &:before {
    right: 0;
    left: auto;
    background-image: url(../img/icons/arrow-right-double.svg); }
  &:hover {
    &:before {
      right: -3px; } } }

.links-nav {
  display: flex;
  justify-content: space-between;
  max-width: 460px;
  width: 100%;
  margin: 0 auto;
  &__prev {
    padding-right: 5px; }
  &__next {
    padding-left: 5px; } }

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; }
  &_sm {
    height: 34px;
    &.btn_min-width {
      min-width: 205px; } }
  &_md {
    height: 52px; } }


.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border-bottom: $input-border;
  border-left: none;
  border-right: none;
  border-top: none;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .btn_md.btn_min-width_big {
    min-width: 340px; }
  .btn_long.btn {
    padding-left: 25px;
    padding-right: 25px;
    font-size: 16px;
    font-family: $site-font-family-2;
    font-weight: 400;
    letter-spacing: $letspacing7; }
  /* end media min width 480 pixels */ }


/* ----- END controls ------------------- */
