/* ----- Variants section ------------------- */

.bl-variants {
  max-width: 1072px;
  margin: 0 auto; }

.bl-variant {
  margin-left: -15px;
  margin-right: -15px;
  padding: 20px 15px 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  background-color: $cl-light;
  margin-bottom: 20px; }

.txt-tt-md {
  color: $cl-site-text;
  font-size: 24px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: $letspacing13;
  margin-bottom: 15px; }

.bl-variant-cap {
  position: relative; }

.bl-variant-cap__txt {
  p {
    color: $cl-site-text;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 10px; }
  a {
    color: #9d4aae;
    font-weight: 700;
    letter-spacing: $letspacing5;
    text-decoration: none;
    &:hover {
      color: #9d4aae;
      text-decoration: underline; } }
  .txt-tt-md {
    margin-right: 50px; } }
.bl-variant-cap__icon {
  position: absolute;
  top: 0;
  right: 0;
  height: 30px;
  >img {
    max-height: 100%; } }

.bl-variant-img {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  &__item {
    display: block;
    text-decoration: none;
    outline: none;
    background-color: $cl-dark;
    border-radius: 5px;
    overflow: hidden;
    transition: 0.2s;
    flex: 0 0 48%;
    max-width: 48%;
    height: 130px;
    margin: 0 1% 5px;
    >img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center top; }
    &:focus, &:hover {
      box-shadow: 0 0 10px rgba(#000, 0.4); } } }

.bl-variant-form {
  margin: 30px 0; }

.callback-rectangle-img {
  border-radius: 0px;
  height: 210px;
  >img {
    position: relative;
    transform: none;
    left: auto;
    top: auto;
    height: 100%;
    width: 100%;
    object-fit: cover; }
  &:before {
    border-radius: 0px; } }




/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .bl-variant-img {
    &__item {
      height: 152px;
      margin-bottom: 10px; } }

  .bl-variant-img__item_long {
    height: 194px; }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .bl-variant-img {
    &__item {
      flex: 0 0 23%;
      max-width: 23%; } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .bl-variant {
    margin-left: 0;
    margin-right: 0; }

  .bl-variant-img {
    &__item {
      flex: 0 0 23%;
      max-width: 23%; } }

  .txt-tt-md {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 17px; }

  .bl-variant-cap__txt {
    margin-bottom: 10px;
    p {
      font-size: 16px;
      line-height: 24px; } }

  .bl-variant-cap {
    display: flex;
    justify-content: space-between;
    &__txt {
      flex-grow: 1; }
    &__icon {
      flex-shrink: 0;
      position: relative;
      top: auto;
      right: auto;
      height: auto;
      margin-left: 10px;
      max-width: 60px; } }

  .bl-variant-form {
    margin-top: 0;
    margin-bottom: 10px;
    .form-group-grid__three-col {
      .btn {
        max-width: 100%;
        min-width: 100px;
        width: 100%;
        font-size: 14px; } } }

  .bl-variants-list {
    margin-bottom: 40px; }

  .bl-variants__tt-first {
    margin-bottom: 50px; }

  .bl-variant-img__item {
    margin-bottom: 20px; }

  .callback-rectangle-img {
    height: 300px; }

  .form-st_quest_sm {
    .form-st__txt-md {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 15px; }
    .form-group {
      margin-bottom: 10px; }
    .form-group_btn {
      margin-top: 20px; } }

  .callback-box_center {
    align-items: center; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .txt-tt-md {
    font-size: 30px;
    line-height: 32px; }

  .bl-variant-cap__txt {
    p {
      font-size: 18px;
      line-height: 30px; } }

  .bl-variant {
    margin-bottom: 40px;
    padding: 25px 30px 15px; }

  .bl-variant-cap__icon {
    max-width: none; }

  .bl-variants__tt-first {
    margin-bottom: 80px; }

  .bl-variants-list {
    margin-bottom: 60px; }

  .bl-variant-form {
    .form-group-grid__three-col {
      .btn {
        font-size: 16px; } } }

  .form-st_quest_sm {
    .form-st__txt-md {
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 40px; }
    .form-group {
      margin-bottom: 20px; }
    .form-group_btn {
      margin-top: 50px; } }

  .callback-rectangle-img {
    height: 445px; }

  .callback-box_rotate-box .callback-box__right_lg {
    margin-right: 60px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {

  .callback-box__right_lg {
    .callback-circle-img_lg {
      width: 640px; } }
  /* end media min width 1200 pixels */ }

/* ----- END Variants section --------------- */
