/* ----- Gallery ------------------- */

.gallery-list {
  &__item {
    margin-bottom: 20px; } }

.gallery-box {
  display: block;
  width: 100%;
  text-decoration: none;
  height: 270px;
  outline: none;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  >img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -1; }
  &-info {
    text-align: center;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 270px;
    padding: 20px 15px 20px;
    color: #fff;
    background-color: rgba(#3d383b, 0.52);
    &__tt {
      font-size: 20px;
      line-height: 24px;
      text-transform: uppercase;
      letter-spacing: $letspacing14;
      margin-bottom: 15px; }
    &__name {
      color: $cl-light;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: $letspacing-standart; } } }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .gallery-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &__item {
      width: 50%;
      padding-left: 10px;
      padding-right: 10px; } }
  /* end media min width 640 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .gallery-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    margin-top: 40px;
    &__item {
      width: 33.3%;
      padding-left: 10px;
      padding-right: 10px; } }
  .gallery-box {
    &-info {
      padding-top: 30px;
      &__body {
        margin-bottom: 30px; } } }

  .gallery-box {
    > img {
      transition: ease-in-out 0.1s; }
    .gallery-box-info {
      opacity: 0;
      transition: ease-in-out 0.1s; }
    &:hover {
      > img {
        transform: scale(1.03); }
      .gallery-box-info {
        opacity: 1; } } }
  /* end media min width 1000 pixels */ }

/* ----- END Gallery --------------- */
