/* ----- Forms ------------------------- */
.st-form {
  margin-bottom: 20px;

  &__tt {
    display: block; } }


.form-group_btn {
  margin-top: 45px; }

.form-group_icon {
  position: relative;
  &__field {
    padding-right: 40px; }
  .icon {
    position: absolute;
    right: 0;
    bottom: 14px;
    z-index: -1; } }
.form-group {
  margin-bottom: 20px;
  &_file {
    margin: 30px 0; }
  &_sm-width {
    max-width: 175px; }
  &_md-width {
    max-width: 410px; }

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0; } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  .form-group__label {
    order: 1; } }

.form-st {
  .form-group__label {
    color: $cl-deep-blush;
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 0;
    letter-spacing: $letspacing4; }

  .form-group__field {
    padding: 0;
    height: 42px;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: $letspacing17;
    background-color: transparent; }

  .form-group_sm-width {
    margin-left: 30px; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-st {
    &__txt-md {
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: $letspacing5;
      margin-bottom: 30px; } }
  /* end media min width 768 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .form-st__txt-md {
    margin-bottom: 44px; }

  .form-st__txt-md_push-sm.form-st__txt-md {
    margin-bottom: 34px; }
  /* end media min width 1200 pixels */ }


/* ----- END Forms --------------------- */
