/* radio */
.jcf-radio {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: default;
  background: #fff;
  border-radius: 50%;
  margin: 0 3px 0 0;
  height: 26px;
  width: 26px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.18);
  span {
    display: none;
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    background: #777;
    border-radius: 100%; }
  input[type="radio"] {
    position: absolute;
    height: 100%;
    width: 100%;
    border: 0;
    margin: 0;
    left: 0;
    top: 0; }
  &.jcf-checked span {
    display: block; } }
