/* ----- Head Banner ------------------- */

.banner-boxes {
  position: relative; }

.banner-box-left {
  background: url(../img/bg-banner-left2.jpg) right bottom no-repeat;
  background-size: cover;
  &__inner {
    padding: 40px 0 20px; } }

.banner-cap-left-wrapp {
  position: relative;
  z-index: 1;
  padding-bottom: 100px;
  .banner-cap__text-img {
    position: absolute;
    bottom: 0;
    left: 0; } }

.banner-cap {
  position: relative;
  padding-bottom: 20px;
  margin-bottom: 20px;
  .title-site, h1, h2, h3, h4 {
    margin-bottom: 0; }
  &:after {
    content: ' ';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #e7dbeb; } }


.banner-box-right {
  background: #ccc url(../img/cap-img.jpg) center center no-repeat;
  background-size: cover;
  &_wrapp-bg-left {
    background-position: left center; }
  &_wrapp-bg-left2 {
    background-position: left -80px center; }

  &__content {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    min-height: 480px;
    padding: 40px 15px;
    .title-site_h2, h2, h1 {
      position: relative; } } }

.contact-banner {
  margin-top: auto;
  padding-top: 30px;
  &__phones {
    margin-bottom: 10px;
    li {
      color: $cl-site-text-second;
      font-family: $site-font-family-2;
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: $letspacing2;
      a {
        text-decoration: none;
        color: $cl-site-text-second;
        outline: none;
        &:hover {
          text-decoration: none;
          color: $cl-site-text-second;
          opacity: 0.8; } } } } }

.form-head {
  max-width: 770px;
  margin: 0 auto;
  box-shadow: 0 10px 54px rgba(183, 183, 183, 0.8);
  border-radius: 10px;
  background-color: $cl-light;
  overflow: hidden;
  &-cap {
    text-align: center;
    padding: 10px 15px;
    &_bg {
      background-color: #f8f7f7; }
    &__tt-cap {
      color: #2e2e2e;
      font-size: 24px;
      line-height: 28px;
      text-transform: uppercase;
      letter-spacing: 0.6px;
      b {
        color: #a84c80; } }
    &__tt {
      color: $cl-site-text;
      font-size: 18px;
      font-weight: 500;
      line-height: 20px;
      margin-bottom: 2px;
      letter-spacing: $letspacing5; }
    &__txt {
      font-size: 12px;
      font-weight: 300;
      line-height: 20px;
      letter-spacing: $letspacing2;
      b {
        font-weight: 400; } } } }

.group-head-radio {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__item {
    margin-right: 30px;
    &:last-child {
      margin-right: 0; } } }

.group-head-radio__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .form-head-group__label {
    margin-right: 10px; } }

.form-head-group {
  box-shadow: 0 0 5px rgba(134, 78, 159, 0.15);
  border-top: 1px solid #cdcdcd;
  padding: 15px 15px 30px;
  &_push-left {
    padding-left: 0px; }
  &__name {
    margin-bottom: 12px;
    color: $cl-site-text-three;
    font-size: 14px;
    font-weight: 500; } }

.form-head-footer {
  text-align: center;
  padding: 36px 20px 23px;
  .form-group {
    max-width: 370px;
    margin: 0 auto 20px;
    &_btn {
      margin-top: 40px; } }
  input.form-group__field {
    @include placeholder-color(#747474);
    padding-left: 0;
    padding-right: 0;
    letter-spacing: $letspacing4;
    height: 22px; } }

.banner-boxes_sm {
  .banner-box-right {
    min-height: 260px;
    padding: 20px 15px; } }
.banner-box-left_mob-hidden {
  padding: 20px 0;
  display: none; }

.banner-subtext {
  color: $cl-site-text;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: $letspacing-standart;
  &_md {
    font-size: 16px;
    line-height: 22px;
    letter-spacing: $letspacing7; } }


.banner-box-right_center {
  display: flex;
  justify-content: center;
  flex-direction: column;
  .title-site, h1, h2, h3 {
    margin: 20px 0; } }

.banner-box-right_slider {
  background: none; }

.banner-slider-nav {
  background-color: rgba(#fff8fc, 0.8);
  padding: 5px 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.banner-slider-wrapp {
  position: relative;
  z-index: 1; }

.sl-arrow {
  position: relative; }

.main-slick-prev {
  padding: 0;
  margin: 0;
  box-shadow: none;
  border: none;
  outline: none;
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  margin-top: -15px;
  right: 35px;
  background: url(../img/icons/arrow-left-banner.svg) center center no-repeat; }

.main-slick-next {
  padding: 0;
  margin: 0;
  box-shadow: none;
  border: none;
  outline: none;
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  margin-top: -15px;
  right: 0px;
  background: url(../img/icons/arrow-right-banner.svg) center center no-repeat; }

.slideshow-pager {
  line-height: 26px; }

.slideshow-pager__value {
  color: #ce5f9e;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.6px; }

.slideshow-pager__all {
  color: #313131;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.45px;
  position: relative; }

.banner-slider-item.slick-slide {
  height: 250px;
  >img {
    object-fit: cover;
    height: 100%;
    width: 100%; } }

.banner-cap-left-wrapp {
  max-width: 520px;
  margin-left: auto; }

.banner-slider-item_wrapp-img-right {
  > img {
    object-position: right center; } }

.banner-slider-item_wrapp-img-left {
  > img {
    object-position: left center; } }

.form-head-footer {
  input, textarea {
    font-size: 12px; } }

.form-head-footer {
  border-top: 1px solid #cdcdcd; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .contact-banner {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &__phones {
      flex-shrink: 0;
      margin-right: 30px;
      margin-bottom: 0; } }

  .banner-box-right__content {
    max-width: 500px;
    margin: 0 auto;
    overflow: hidden; }
  .banner-title-line {
    position: relative;
    padding-bottom: 1px;
    margin-bottom: 20px;
    &:before {
      content: ' ';
      position: absolute;

      left: -30px;
      top: 0;
      width: 1px;
      height: 410px;
      background-color: #e7dbeb; }
    &:after {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: -65px;
      right: 0;
      height: 1px;
      background-color: #e7dbeb; } }

  .form-head-group {
    padding: 11px 20px 14px 35px; }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .form-head-grid {
    display: flex;
    flex-wrap: wrap;
    &__half-col {
      flex-basis: 50%;
      max-width: 50%; } }

  .form-head-grid {
    .form-head-group {
      &:nth-child(even) {
        border-left: 1px solid #cdcdcd; }
      &:nth-child(odd) {
        padding-left: 40px; } } }

  .form-head-cap_big {
    padding-top: 21px;
    padding-bottom: 21px; }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner-box-left_mob-hidden {
    display: block; }
  .banner-boxes_sm {
    .banner-box-left {
      background: #FBEAF4 url(../img/bg-banner-left.jpg) left center no-repeat;
      background-size: auto 100%; } }

  .banner-bread-crumbs {
    padding-bottom: 8px;
    margin-bottom: 10px;
    position: relative;
    padding-left: 34px;
    padding-top: 15px;
    &_alone {
      margin-bottom: 0;
      &:before, &:after {
        display: none; } }
    .bread-crumbs {
      margin-bottom: 0;
      padding-bottom: 0; }

    &:before {
      content: ' ';
      position: absolute;
      left: -22px;
      bottom: 0;
      background-color: #fff;
      height: 1px;
      width: 372px; }
    &:after {
      content: ' ';
      position: absolute;
      left: 0;
      top: 0;
      height: 144px;
      width: 1px;
      background-color: #fff; } }

  .banner-subtext {
    padding-left: 34px; }

  .banner-boxes_sm {
    .banner-box-left_mob-hidden {
      overflow: hidden; } }


  .form-head-grid {
    .form-head-group {
      &:nth-child(odd) {
        padding-left: 110px; } } }

  .banner-slider-item.slick-slide {
    height: 350px; }

  .banner-slider-nav {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 30px 40px; }

  .main-slick-prev {
    right: auto;
    left: 10px; }

  .main-slick-next {
    right: 10px; }

  .sl-arrow {
    position: static; }

  .form-head-group__name {
    font-size: 18px;
    letter-spacing: 0.45px;
    margin-bottom: 16px; }
  .form-head-group_push-left {
    padding-left: 25px; }
  .form-head-group {
    padding-top: 17px;
    padding-bottom: 14px; }
  .group-head-radio__item .form-head-group__label {
    font-size: 16px;
    letter-spacing: 0.4px;
    font-weight: 300; }

  .form-head-group {
    .jcf-select .jcf-select-text {
      font-size: 16px;
      font-weight: 300; } }

  .s-range {
    color: #2c2c2c;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 0.4px; }

  .form-head-footer .form-group {
    margin-bottom: 25px; }

  .form-head-footer .form-group_btn {
    margin-top: 45px; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-boxes {
    position: relative;
    &_sm {
      .banner-box-left {
        min-height: 110px;
        padding-top: 4px;
        padding-bottom: 4px; } }
    .banner-box-right {
      position: absolute;
      top: -134px;
      bottom: 0;
      left: 50%;
      right: 0;
      margin-left: -44px; } }
  .banner-bread-crumbs {
    margin-bottom: 7px;
    &:before,
    &:after {
      display: block; } }
  .form-head {
    margin-left: 0;
    position: relative;
    z-index: 10; }
  .banner-box-right__content {
    height: 100%;
    margin-left: 180px;
    padding-top: 130px;
    padding-bottom: 42px;
    p {
      margin-bottom: 45px; } }
  .banner-box-left__inner {
    margin-right: 390px;
    padding-top: 20px; }
  .banner-cap-left-wrapp {
    margin-right: 10px; }
  .banner-title-line {
    margin-bottom: 26px; }
  .contact-banner {
    padding-left: 25px; }
  .banner-box-left__inner {
    padding-bottom: 25px; }
  .banner-box-right__content {
    padding-right: 30px; }
  .banner-box-left__content-wrapp {
    margin-right: 575px; }
  .banner-boxes_sm .banner-box-right {
    padding-left: 30px; }
  .banner-box-right_push-left_md.banner-box-right {
    padding-left: 80px; }
  .banner-box-right_push-left_sm.banner-box-right {
    padding-left: 50px; }
  .wrapp-banner-content {
    .banner-box-left__content-wrapp {
      margin-right: 505px; }
    .bread-crumbs {
      li {
        font-size: 12px;
        letter-spacing: $letspacing2; } } }

  .banner-box-left__inner {
    margin-right: 565px; }
  .banner-slider-wrapp {
    height: 100%;
    .slick-slider, .slick-list, .slick-track, .banner-slider-item.slick-slide {
      height: 100%; } }

  .banner-cap .title-site_h1_big3 {
    font-size: 28px; }

  .banner-box-left {
    background-position: right 540px bottom 0;
    background-size: auto 100%; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner-boxes .banner-box-right {
    top: -158px; }
  .wrapp-banner-content-sm {
    .banner-box-left__content-wrapp {
      margin-right: 712px; }
    .banner-boxes_sm .banner-box-left {
      padding-top: 4px;
      padding-bottom: 10px; }
    .banner-bread-crumbs {
      padding-top: 9px;
      margin-bottom: 11px; } }
  .wrapp-banner-content {
    .banner-box-left__content-wrapp {
      margin-right: 635px; }
    .bread-crumbs {
      li {
        font-size: 12px;
        letter-spacing: $letspacing2; } }
    .banner-bread-crumbs {
      padding-top: 7px;
      margin-bottom: 5px; }
    .banner-boxes_sm .banner-box-left {
      padding-top: 4px;
      padding-bottom: 4px; }
    .banner-box-left {
      padding-top: 16px; }
    .banner-box-right_push-left_md {
      padding-left: 100px; } }
  .banner-box-right__content {
    margin-left: 255px; }
  .contact-banner__phones {
    margin-right: 45px; }
  .banner-box-right__content {
    max-width: 500px; }
  .banner-box-left {
    background-position: right calc(50% + 504px) bottom 0;
    background-size: auto 100%; }

  .banner-box-left__content-wrapp {
    margin-right: 632px; }
  .banner-box-right_push-left_lg.banner-box-right {
    padding-left: 160px; }

  .banner-cap .title-site_h1_big3 {
    font-size: 40px;
    line-height: 48px; }
  .banner-cap-left-wrapp {
    max-width: none;
    margin-left: 0; }
  .banner-box-left__inner {
    margin-right: 630px;
    padding-top: 62px; }
  .banner-cap {
    padding-bottom: 33px;
    margin-bottom: 42px;
    &:before {
      content: ' ';
      position: absolute;
      left: -30px;
      top: -10px;
      width: 1px;
      height: 343px;
      background-color: #e7dbeb; } }
  .banner-cap:after {
    width: 518px;
    left: -66px; }
  .banner-cap-left-wrapp {
    min-height: 452px; }
  .banner-cap-left-wrapp .banner-cap__text-img {
    left: -9px; }
  .banner-box-left__inner {
    padding-bottom: 17px; }
  .banner-slider-nav {
    padding: 34px 40px; }
  /* end media min width 1200 pixels */ }


/* Min width 1700 pixels */
@media only screen and (min-width: 1700px) {
  .banner-box-left {
    background-position: right 103% bottom 0;
    background-size: 50% 100%; }
  /* end media min width 1700 pixels */ }


/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .banner-box-right {
    &_wrapp-bg-left2 {
      background-position: left center; } }
  /* end media min width 1900 pixels */ }

/* ----- END Head Banner --------------- */
