/* ----- Card ------------------- */

.list-card-item {
  text-align: center;
  .card {
    max-width: 300px;
    margin: 0 auto; }
  margin-bottom: 30px; }

.catalog-list {
  &__item {
    margin-bottom: 20px;
    text-align: center;
    .card {
      max-width: 300px;
      margin: 0 auto; } } }

.card {
  position: relative;
  text-decoration: none;
  outline: none;
  display: inline-block;
  vertical-align: top;
  max-width: 100%;
  width: 100%;
  text-align: center;
  box-shadow: $shadow4;
  border-radius: 10px;
  border: 1px solid #e5e5e5;
  background-color: $cl-light;
  padding: 5px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img {
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    display: block;
    height: 240px;
    line-height: 240px;
    text-align: center;
    flex-shrink: 0;
    >img {
      max-height: 100%; }
    &_sm {
      height: 230px;
      line-height: 230px; }
    &_border {
      border: 1px solid #e5e5e5;
      border-radius: 5px; }
    &_full {
      >img {
        object-fit: cover;
        height: 100%;
        width: 100%; } }
    &_full_top {
      object-fit: center top; } }


  &-content {
    padding: 18px 5px 8px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    &_sm {
      padding-top: 12px;
      padding-bottom: 1px;
      .card-content__btn {
        margin-bottom: 16px; } }
    &_md {
      padding-top: 15px;
      padding-bottom: 3px; }
    &__tt {
      color: $cl-site-text;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: $letspacing5;
      margin-bottom: 8px; }
    &__txt {
      color: $cl-site-text;
      font-size: 14px;
      font-weight: 300;
      line-height: 18px;
      letter-spacing: $letspacing-standart;
      margin-bottom: 6px;
      &_sm-width {
        max-width: 195px;
        margin-left: auto;
        margin-right: auto; } }
    &__txt-st {
      color: $cl-site-text;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: $letspacing-standart;
      margin-bottom: 10px;
      &_wrapp {
        max-width: 220px;
        margin-left: auto;
        margin-right: auto; } }

    &__name {
      color: $cl-site-text;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: $letspacing5;
      margin-bottom: 12px;
      &_small {
        font-size: 14px;
        letter-spacing: $letspacing-standart;
        margin-bottom: 7px; }
      &-sm {
        color: $cl-site-text;
        font-size: 15px;
        line-height: 18px;
        letter-spacing: 0.38px;
        margin-bottom: 10px; } }
    &__price {
      color: $cl-сadillac;
      font-family: $site-font-family-2;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: $letspacing-standart;
      margin-bottom: 15px;
      &-md {
        color: $cl-сadillac;
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: $letspacing7;
        margin-bottom: 15px; } }
    &__btn {
      margin-top: auto;
      margin-bottom: 20px; } } }

.card-hover {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(#3c3445, 0.33);
  text-align: center;
  color: $cl-light;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: $letspacing5; }

a.card__img {
  >img {
    transition: ease-in-out 0.2s; }
  &:hover {
    >img {
      transform: scale(1.03); }
    .card-hover {
      display: block; } } }


.about-cards {
  margin-bottom: 40px; }

.about-card {
  position: relative;
  display: block;
  text-decoration: none;
  outline: none;
  color: #2e2e2e;
  box-shadow: 0 0 51px rgba(0, 0, 0, 0.13);
  border-radius: 10px;
  overflow: hidden;
  background-color: #fff;
  margin-bottom: 20px;
  &__img {
    height: 205px;
    max-width: 100%;
    width: 100%;
    >img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      object-position: center bottom; } }
  &__content {
    padding: 10px 15px 10px; }
  &:hover {
    text-decoration: none; }
  &__time {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(to right, #ef91c6 0%, #e569af 100%);
    background-clip: text;
    text-shadow: 0 0 6px rgba(250, 94, 123, 0.5);
    color: #2e2e2e;
    font-size: 24px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0.75px;
    margin-bottom: 10px; }
  &__name {
    color: #2e2e2e;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.45px;
    margin-bottom: 10px; }
  &__txt {
    color: #2e2e2e;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    letter-spacing: 0.4px;
    margin-bottom: 10px; } }

.desc-bl {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 40px;
  &__tt {
    color: #cb5899;
    font-size: 26px;
    font-weight: 700;
    letter-spacing: 0.85px;
    line-height: 30px;
    margin-bottom: 10px; }
  &__txt-md {
    color: #242424;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.6px;
    line-height: 24px;
    margin-bottom: 20px; } }

.list-card_sm, .catalog-list_sm, .slider-product_sm {
  margin-bottom: 30px;
  .list-card-item {
    margin-bottom: 10px; }

  .card-content {
    padding: 10px 5px 1px; } }

.card_no-push.card {
  padding: 0;
  .card__img_border {
    border-left: none;
    border-right: none;
    border-top: none;
    border-radius: 10px 10px 0 0; }
  .card__img {
    border-radius: 10px 10px 0 0; } }

a.card {
  transition: 0.2s;
  &:hover,
  &:focus {
    box-shadow: 2px 2px 5px rgba(#000, 0.4); } }



.catalog-list_sm {
  display: flex;
  flex-wrap: wrap;
  margin-left: -3px;
  margin-right: -3px;
  .catalog-list__item {
    padding-left: 3px;
    padding-right: 3px;
    max-width: 50%;
    flex: 0 0 50%;
    margin-bottom: 5px; }
  .card-content__name_small {
    font-size: 12px;
    line-height: 14px; }
  .card__img {
    height: 150px;
    line-height: 150px; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .about-cards {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%;
    .about-card {
      flex: 0 0 48%;
      margin-left: 1%;
      margin-right: 1%; } }
  .catalog-list_sm {
    .catalog-list__item {
      margin-bottom: 15px; }
    .card__img {
      height: 200px;
      line-height: 200px; } }

  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .list-card {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &_center {
      justify-content: center; }
    &-item {
      width: 50%;
      padding: 0 10px; } }

  .catalog-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
    &__item {
      padding-left: 5px;
      padding-right: 5px;
      max-width: 50%;
      flex: 0 0 50%;
      margin-bottom: 10px; } }

  .catalog-list_sm {
    .catalog-list__item {
      margin-bottom: 15px; }
    .card-content__name_small {
      font-size: 14px;
      line-height: 16px; }
    .card__img {
      height: 240px;
      line-height: 240px; } }
  /* end media min width 640 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .list-card {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &-item {
      width: 33.3%;
      padding: 0 10px;
      .card-content {
        padding-left: 10px;
        padding-right: 10px; } } }

  .list-card-item_hidden-tabl {
    display: none; }

  .link-next {
    margin-top: 15px; }

  .about-card {
    &__time {
      font-size: 30px;
      line-height: 32px; }
    &__name {
      font-size: 18px;
      line-height: 24px; }
    &__txt {
      font-size: 16px;
      line-height: 22px; } }

  .about-cards {
    .about-card {
      flex: 0 0 31.3%; } }

  .about-card_tabl-hidden.about-card {
    display: none; }

  .desc-bl {
    &__tt {
      font-size: 34px;
      line-height: 40px; }
    &__txt-md {
      font-size: 24px;
      line-height: 30px; } }

  .card__img_md.card__img {
    height: 253px;
    line-height: 253px; }

  .catalog-list_sm {
    .card__img {
      height: 180px;
      line-height: 180px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .about-card_tabl-hidden.about-card {
    display: block; }
  .link-next {
    margin-top: 35px; }
  .list-card {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &-item {
      width: 25%;
      padding: 0 10px;
      &_hidden-tabl {
        display: block; }
      .card-content {
        padding-left: 10px;
        padding-right: 10px; } } }

  .catalog-list {
    &__item {
      max-width: 33.3%;
      flex: 0 1 33.3%; } }

  .about-cards {
    .about-card {
      flex: 0 0 23%; } }

  .catalog-list_sm .catalog-list__item {
    flex: 0 0 33.3%;
    max-width: 33.3%; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .list-card_wrapp {
    margin-left: 0;
    margin-right: 0;
    padding-left: 18px;
    padding-right: 18px; }
  .catalog-list_push-desk-top {
    padding-top: 33px; }
  .catalog-list {
    margin-left: -10px;
    margin-right: -10px;
    &__item {
      padding-left: 10px;
      padding-right: 10px;
      margin-bottom: 20px; } }

  .about-card__content {
    padding: 17px 23px 11px 18px; }
  .about-card__name {
    margin-bottom: 16px; }
  .about-cards {
    margin-bottom: 33px; }
  .desc-bl {
    margin-bottom: 80px; }

  .catalog-list_sm {
    margin-left: -5px;
    margin-right: -5px; }
  .catalog-list_sm .catalog-list__item {
    padding-left: 5px;
    padding-right: 5px; }
  .catalog-list_sm .card__img {
    height: 253px;
    line-height: 253px; }
  /* end media min width 1200 pixels */ }

/* ----- END Card --------------- */
