/* checkbox */
.jcf-checkbox {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: default;
  background: #fff;
  border: 1px solid #555f76;
  border-radius: 2px;
  margin: 0 13px 0 0;
  height: 12px;
  width: 12px;
  flex-shrink: 0;
  span {
    position: absolute;
    display: none;
    top: 50%;
    left: 50%;
    background: url(../img/icons/check-sm.svg) center center no-repeat;
    background-size: contain;
    width: 6px;
    height: 6px;
    margin-top: -3px;
    margin-left: -3px; } }
