.jcf-checkbox {
  input[type="checkbox"] {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 0;
    margin: 0;
    left: 0;
    top: 0; }
  &.jcf-checked span {
    display: block; }
  span {
    box-sizing: content-box; } }


.fake-radio {
  .jcf-radio {
    width: 22px;
    height: 22px;
    border-radius: 2px;
    border: 1px solid $cl-alto; }
  .jcf-radio span {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 0px;
    background: url(../img/icons/check.svg) center center no-repeat; } }

