/* ----- Callback ------------------- */
.callback-box__right {
  margin-bottom: 20px; }

.callback-box__left {
  max-width: 432px; }

.callback-circle-img {
  width: 290px;
  height: 290px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  &:before {
    content: ' ';
    position: absolute;
    top: 19px;
    bottom: 19px;
    right: 19px;
    left: 19px;
    border-radius: 50%;
    border: 1px solid rgba($cl-light, 0.8); }
  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 105%;
    z-index: -1; }
  &_lg {
    >img {
      max-width: 136%; } } }

.callback-box__txt {
  margin-top: -70px;
  position: relative;
  z-index: 1; }
.callback-name {
  color: $cl-site-text;
  font-size: 30px;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: $letspacing4;
  text-shadow: 0 0 129px #fff;
  text-shadow: rgba(#fff, 1) 0px 0px 5px, rgba(#fff, 1) 0px 0px 10px, rgba(#fff, 1) 0px 0px 15px, rgba(#fff, 1) 0px 0px 20px, rgba(#fff, 1) 0px 0px 30px, rgba(#fff, 1) 0px 0px 40px, rgba(#fff, 1) 0px 0px 50px; }

.callback-prof {
  color: $cl-site-text;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: $letspacing7; }

.callback-block__right {
  margin-bottom: 30px; }

.img-inner-borderf {
  display: inline-block;
  position: relative;
  &:before {
    content: ' ';
    position: absolute;
    top: 15px;
    bottom: 15px;
    left: 20px;
    right: 20px;
    border: 1px solid #e6e6e6; } }

.big-text {
  font-size: 30px;
  line-height: 1.1;
  font-weight: 700;
  letter-spacing: $letspacing4;
  text-transform: uppercase; }

.block-video {
  position: relative;
  min-height: 250px;
  display: block;
  text-decoration: none;
  outline: none;
  max-width: 100%;
  margin-bottom: 30px;
  overflow: hidden;
  box-shadow: 0 5px 35px rgba(0, 0, 0, 0.08);
  background-color: $cl-dark;
  transition: ease-in-out 0.15s;
  &:hover {
    box-shadow: 0 5px 35px rgba(0, 0, 0, 0.08)7;
    .icon-play {
      opacity: 1; }
    >img {
      transform: translate(-50%, -50%) scale(1.03); } }
  .icon-play {
    display: inline-block;
    vertical-align: middle;
    background: url(../img/icons/video-play-bg.svg) center center no-repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 53px;
    height: 53px;
    margin-top: -27px;
    margin-left: -27px;
    background-size: 100% 100%;
    z-index: 1;
    text-align: center;
    line-height: 53px;
    opacity: 0.8;
    &:before {
      content: "\f04b";
      line-height: 53px;
      display: inline-block;
      font-family: FontAwesome;
      font-size: inherit;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      font-weight: normal;
      font-style: normal;
      background-image: $gradient-st;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      color: #333;
      font-size: 14px;
      text-align: center;
      margin-left: 3px; } }
  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    transition: ease-in-out 0.15s; } }

.txt-box__italic {
  color: $cl-site-text-four;
  font-family: $site-font-family-3, serif;
  font-size: 26px;
  font-weight: normal;
  font-style: italic;
  line-height: 36px;
  letter-spacing: $letspacing16;
  margin-bottom: 20px; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .big-text {
    font-size: 36px; }
  .callback-box {
    display: flex;
    justify-content: space-between;
    &__right {
      order: 1;
      flex-shrink: 0; }
    &__left {
      margin-right: 50px; } }
  .callback-box_rotate-box {
    .callback-box__right {
      order: 0;
      margin-right: 50px; }
    .callback-box__left {
      margin-right: 0; } }

  .callback-block {
    display: flex;
    justify-content: space-between;
    &__right,
    &__left {
      width: 48%; }
    &__right {
      &_order {
        order: 1; } } }

  .img-inner-border {
    position: relative;
    overflow: hidden;
    z-index: 1;
    min-height: 550px;
    width: 100%;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: none;
      max-width: 200%;
      z-index: -1; } }

  .img-inner-border_sm {
    min-height: 350px; }

  .block-video {
    min-height: 315px; }
  .callback-block__right {
    margin-bottom: 0; }
  .about-grid__half-col_push-bt {
    padding-bottom: 20px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .callback-circle-img {
    width: 440px;
    height: 440px; }
  .callback-circle-img_lg {
    width: 483px;
    height: 483px; }
  .callback-name {
    color: $cl-site-text;
    font-size: 36px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: $letspacing4;
    margin-bottom: 20px; }
  .callback-box__txt {
    margin-top: -110px; }
  .callback-box__right {
    flex-grow: 1;
    padding-top: 10px; }
  .callback-box_rotate-box {
    .callback-circle-img {
      margin-left: 0; }
    .callback-box__right {
      flex-shrink: 0;
      flex-grow: 0; }
    .callback-box__left {
      flex-grow: 1;
      max-width: none;
      .form-st {
        max-width: 410px; } } }
  .callback-circle-img {
    margin-left: auto; }

  .callback-block {
    &_center {
      align-items: center; }
    &__right,
    &__left {
      width: 48%; }
    &__right {
      &_order {
        order: 1; } } }

  .img-inner-border {
    &:before {
      top: 25px;
      bottom: 25px;
      left: 30px;
      right: 30px; } }

  .txt-box {
    h4, .title-site_h4 {
      margin-bottom: 20px; }
    p {
      margin-bottom: 30px; } }
  .txt-box__italic {
    margin-bottom: 30px; }

  .img-inner-border_sm {
    min-height: 390px; }
  .img-inner-border_full-height {
    min-height: 420px;
    height: 100%; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .callback-box_sm {
    padding: 75px 26px 0 65px;
    &_push-top {
      padding-top: 95px;
      padding-bottom: 10px; }
    .callback-box__right {
      max-width: 565px; }
    .callback-box__txt {
      margin-top: -148px; } }

  .callback-box {
    .form-st {
      .title-site, h1, h2 {
        font-size: 32px;
        letter-spacing: $letspacing15; } } }
  .callback-box_rotate-box {
    .callback-box__right {
      margin-right: 150px; }
    .callback-box__left {} }

  .callback-block {
    &__right,
    &__left {
      width: 48.5%; } }
  /* end media min width 1200 pixels */ }



/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .img-inner-border_lg-right {
    min-width: 680px; }
  /* end media min width 1440 pixels */ }

