/* ----- Project ------------------- */
.product-block {
  border-bottom: 1px solid $cl-silver; }

.product-slider {
  margin-bottom: 13px; }

.product-slider-thumb {
  padding: 0 20px; }

.product-slider-thumb-item {
  margin: 0 5px; }

.product-slider-thumb-item-img {
  width: 100%;
  text-align: center;
  height: 72px;
  line-height: 72px;
  >img {
    display: inline-block;
    vertical-align: middle;
    max-height: 100%; } }

.product-block-info__cap {
  .title-site, h1, h2 , h3, h4 {
    margin-bottom: 10px;
    letter-spacing: 0.55px;
    font-size: 22px; }
  p {
    color: $cl-site-text;
    letter-spacing: $letspacing5;
    font-size: 18px;
    line-height: 22px; } }

.product-slider-box {
  margin-bottom: 30px; }

.product-block-info__btn {
  margin-bottom: 30px; }

.product-blocks__info {
  padding: 40px 0 60px;
  line-height: 24px;
  font-size: 14px;
  p {
    margin-bottom: 18px; } }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .product-slider-box {
    display: flex;
    justify-content: flex-start;
    .product-slider-wrapp {
      flex-shrink: 0;
      flex-basis: 400px;
      max-width: 400px; }
    .product-slider-thumb-wrapp {
      flex-shrink: 0;
      flex-basis: 122px;
      max-width: 122px;
      min-width: 122px; } }
  .product-slider-img {
    position: relative;
    overflow: hidden;
    min-height: 332px;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: 110%; } }
  .product-slider-thumb-item {
    margin: 5px 13px; }
  .product-slider-thumb-item-img {
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
    min-height: 72px;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: 120%; } }

  .product-slider-thumb {
    padding: 40px 0; }
  .product-slider-thumb-wrapp {
    height: 100%; }

  .product-slider-box {
    .slick-prev, .slick-next {
      left: 50%;
      right: auto;
      margin-top: 0;
      width: 90px;
      height: 40px;
      margin-left: -45px;
      &:before {
        width: 24px;
        height: 13px;
        margin-top: -6px;
        margin-left: -12px; } }
    .slick-prev {
      top: auto;
      bottom: 0;
      &:before {
        background-image: url(../img/icons/arrow-down-slider.svg); } }
    .slick-next {
      top: 0;
      &:before {
        background-image: url(../img/icons/arrow-top-slider.svg); } } }
  /* end media min width 640 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .product-block {
    display: flex;
    .product-slider-box {
      flex-shrink: 0; }
    .product-block-info {
      flex-grow: 1; } }

  .product-slider {
    height: 100%;
    margin-bottom: 0;
    .slick-list, .slick-track, .product-slider-img {
      height: 100%; } }
  .product-slider-box {
    margin-bottom: 0; }
  .product-slider-box .product-slider-thumb-wrapp {
    display: flex;
    justify-content: center;
    flex-direction: column; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .product-slider-box .product-slider-wrapp {
    flex-basis: 600px;
    max-width: 600px; }
  .product-block .product-block-info {
    padding-left: 16px; }
  .product-block-info__cap {
    margin-bottom: 24px; }
  .product-block-info__content {
    margin-bottom: 16px; }
  .product-block-info {
    display: flex;
    flex-direction: column; }
  .product-block-info__btn {
    margin-top: auto;
    margin-bottom: 47px; }
  /* end media min width 1200 pixels */ }

