/* ----- Step2 EDITS 05052020 ------------------- */

.logo_header {
  position: relative;
  z-index: 2; }

.head-work {
  border-radius: 0 10px 10px 0;
  background-color: #9257ab;
  color: #fff;
  padding: 10px 15px 10px 25px;
  display: inline-block;
  position: absolute;
  top: 97px;
  left: 0;

  &__name {
    color: #fff;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: 0.35px;
    margin-bottom: 2px; }

  &__tt {
    font-size: 18px;
    line-height: 1.1;
    color: #fff;
    font-weight: 900;
    letter-spacing: 0.45px;
    margin-bottom: 10px; }

  &__link {
    color: #d3c0ff;
    font-size: 12px;
    font-weight: 300;
    line-height: 1.2;
    letter-spacing: 0.3px;
    &:hover, &:focus {
      text-decoration: none;
      color: lighten(#d3c0ff, 10%); } } }


.f-contact_btn {
  margin-bottom: 30px;
  > * {
    margin-bottom: 10px; } }

.develop {
  display: inline-block;
  position: relative;
  text-decoration: none;
  outline: none;
  padding: 8px 25px 8px 15px;
  border-radius: 16px;
  background-color: #2a2342;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  &__name {
    opacity: 0.76;
    color: #fff;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: 0.35px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    position: relative;
    bottom: -1px; }
  &__img {
    position: relative;
    top: -2px; }
  &:after {
    content: ' ';
    position: absolute;
    top: -5px;
    right: 5px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #000;
    background-image: linear-gradient(-161deg, #cd078c 0%, #4d0689 100%);
    animation: {
      name: dot-push;
      duration: 10s;
      timing-function: linear;
      iteration-count: infinite;
      direction: alternate-reverse; } }

  &:hover, &:focus {
    background-color: darken(#2a2342, 10%); } }

@keyframes dot-push {
  0% {
    transform: translateX(0); }
  100% {
    transform: translateX(-70px); } }

.form-group_status {
  .btn {
    margin-top: 20px; } }

.status-name {
  color: #2e2e2e;
  font-size: 16px;
  line-height: 1.2;
  letter-spacing: 0.6px;
  margin-bottom: 10px; }

.status-state {
  color: #2e2e2e;
  font-size: 16px;
  line-height: 1.2;
  margin-bottom: 10px;
  &__green {
    color: #49be25;
    letter-spacing: 0.6px;
    font-weight: 500; } }

.status-data {
  color: #2e2e2e;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 1.2px;
  padding: 14px 15px;
  border-radius: 5px;
  background-color: #f8f8f8;
  margin-bottom: 20px; }



/* max width 767 pixels */
@media only screen and (max-width: 767px) {
  .header {
    min-height: 192px; }

  .head-soc {
    padding-left: 125px; }
  /* end media max width 767 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .head-work {
    display: none; }
  .f-contact_btn {
    margin-bottom: 0; }

  .st-modal_status {
    max-width: 730px;
    padding: 60px 55px 20px;
    .title-site_h1 {
      margin-bottom: 40px; } }

  .form-group_status {
    position: relative;
    z-index: 1;
    .btn {
      min-width: 170px;
      position: absolute;
      bottom: 0;
      right: 0; }
    input.form-group__field {
      width: calc(100% - 20px); } }

  .status-grid {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    &__half-col {
      width: 40%;
      &:nth-child(2) {
        width: 58%; } } }

  .status-data {
    margin-right: -55px;
    border-radius: 5px 0 0 5px; }

  .status-state {
    padding-left: 15px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .f-ps-list li {
    margin: 0 30px 17px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .head-work {
    display: inline-block;
    text-align: right;
    padding: 0;
    left: 0;
    right: 50%;
    margin-right: 483px;
    z-index: 111;
    top: 146px;
    &__inner {
      text-align: left;
      display: inline-block;
      padding: 15px 12px 10px 0; } }

  .social-sm, .head-soc-phone-box {
    padding-left: 100px; }

  #wrapper.sticky .header {
    .head-work {
      display: none; } }

  .head-soc-btn {
    padding-left: 24px;
    margin-left: 25px; }

  .head-soc-btn__item_first:before {
    left: -24px; }

  .head-soc {
    padding-left: 20px; }

  /* end media min width 1200 pixels */ }


/* ----- END Step2 EDITS 05052020 --------------- */
