/* ----- Review ------------------- */

.review-box {
  &__value {
    color: $cl-dusty-gray;
    font-family: $site-font-family-4;
    font-size: 36px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: $letspacing4;
    position: relative;
    display: block;
    width: 100%;
    margin-bottom: 15px;
    &:before {
      content: ' ';
      position: absolute;
      top: 50%;
      margin-top: -1px;
      height: 1px;
      left: 0;
      right: 0;
      background-color: #efefef;
      z-index: 1; }
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      left: 0;
      width: 170px;
      margin-top: -1px;
      height: 1px;
      background-color: #efc94c;
      background-image: $gradient-st;
      z-index: 2; }
    span {
      display: inline-block;
      padding-right: 10px;
      background-color: #fff;
      position: relative;
      z-index: 5; } } }

.review-box-grid {
  &__left {
    margin-bottom: 20px;
    text-align: center; } }

.review-screen {
  border: 1px solid #efefef;
  display: inline-block;
  max-width: 100%;
  text-align: center; }

.name-review {
  color: $cl-site-text;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: $letspacing17;
  margin-bottom: 15px;
  padding-right: 80px;
  overflow: hidden;
  text-align: left;
  span {
    display: inline-block;
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      margin-top: -1px;
      left: 100%;
      margin-left: 15px;
      width: 57px;
      height: 1px;
      box-shadow: $shadow3;
      background-color: #efc94c;
      background-image: $gradient-st; } } }

.review-name-project {
  color: $cl-site-text;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: $letspacing7;
  margin-bottom: 15px; }

.review-txt {
  color: $cl-site-text;
  font-size: 14px;
  font-weight: 300;
  font-style: italic;
  line-height: 18px;
  margin-bottom: 25px;
  letter-spacing: $letspacing17; }

.review-slider-sm__item {
  padding: 5px; }

.img-review-sm {
  height: 105px;
  position: relative;
  width: 100%;
  display: block;
  text-decoration: none;
  outline: none;
  max-width: 200px;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;

  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 150%; } }

.review-slider-sm {
  padding: 0 10px; }

.slider-wrapp-review {
  padding-bottom: 60px;
  margin-left: -15px;
  margin-right: -15px; }

.slider-review-item {
  padding-left: 15px;
  padding-right: 15px; }

.list-review {
  &__item {
    margin-bottom: 50px; } }
/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .review-box {
    &__value {
      &:after {
        width: 270px; } } }

  .review-box-grid {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    &__left {
      width: 40.5%;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      margin-bottom: 0;
      .review-screen {
        flex-grow: 1;
        >img {} } }

    &__right {
      width: 59.5%;
      padding-bottom: 20px;
      padding: 0 0 20px 20px;
      border-bottom: 1px solid #efefef;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      .review-slider-sm {
        margin-top: auto; } } }

  .review-slider-sm.slick-slider {
    padding: 0 35px;
    .slick-prev {
      left: 0; }
    .slick-next {
      right: 0; } }

  .review-screen_long-img {
    overflow: hidden;
    min-height: 300px;
    position: relative;
    img {
      position: absolute;
      left: 50%;
      top: 0;
      max-width: 100%;
      transform: translateX(-50%); } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .review-slider-sm.slick-slider {
    padding: 0 55px; }

  .review-txt {
    font-size: 16px;
    line-height: 24px; }

  .slider-wrapp-review {
    padding-bottom: 105px; }

  .review-name-project {
    margin-bottom: 18px; }

  .review-box-grid__right {
    padding-bottom: 35px; }
  .review-txt {
    margin-bottom: 18px; }
  .review-slider-sm.slick-slider {
    padding: 0 65px; }
  .review-slider-sm.slick-slider .slick-prev {
    left: 7px; }
  .review-slider-sm.slick-slider .slick-next {
    right: 7px; }
  /* end media min width 1000 pixels */ }

/* ----- END Review --------------- */
