/* ----- Footer ------------------------ */
.footer {
  &-social {
    padding: 20px 0 0;
    .title-site, h1, h2, h3, h4 {
      margin-bottom: 30px; } } }

.footer-social-box {
  background-color: $cl-alabaster2; }

.social {
  padding: 31px 0 11px;
  text-align: center;

  &-item {
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px 20px;
    color: $cl-site-text;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
    text-transform: uppercase;

    &__link {
      text-decoration: none;
      outline: none;
      color: $cl-site-text;
      .icon, .icon-s {
        margin-right: 8px; }

      span {
        display: inline-block;
        vertical-align: bottom; }


      &:hover {
        text-decoration: none;
        color: $cl-site-text;
        outline: none;
        opacity: 0.7; } } } }

.f-box {
  text-align: center;
  padding: 30px 0 0;
  &__logo {
    margin-bottom: 15px; } }

.f-contact-box {
  &__item {
    margin-bottom: 20px;
    &_btn {
      margin-top: 30px; } }
  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 22px;
    text-align: center; } }

.f-contact-box__inner {
  display: inline-block;
  position: relative;
  color: $cl-site-text;
  font-weight: 300;
  line-height: 20px;
  padding-left: 30px;
  text-decoration: none;
  outline: none;
  &:hover {
    color: $cl-site-text; } }

.icon-marker {
  background: url(../img/icons/marker-sm.svg) center center no-repeat;
  width: 12px;
  height: 16px;
  display: inline-block;
  vertical-align: middle; }

.f-nav {
  text-align: center;
  padding-bottom: 20px; }

.f-nav-menu {
  li {
    color: $cl-site-text-second;
    font-size: 12px;
    font-weight: 300;
    line-height: 16px;
    margin: 10px 0;
    a {
      color: $cl-site-text-second;
      text-decoration: none;
      &:hover {
        color: #9e49ae;
        text-decoration: none; } }
    &.active {
      a {
        color: #9e49ae; } } } }

.f-ps {
  background-color: $cl-alabaster2;
  padding: 18px 0 1px;
  &-list {
    text-align: center;
    li {
      margin: 0 10px 17px;
      color: $cl-site-text;
      font-size: 14px;
      font-weight: 300;
      line-height: 20px;
      display: inline-block;
      vertical-align: middle;
      a {
        color: $cl-site-text;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
          color: $cl-site-text; } }
      &.font-medium {
        font-weight: 500; } } } }

.copy {
  color: $cl-site-text;
  font-size: 12px;
  font-weight: 300;
  line-height: 20px;
  letter-spacing: $letspacing2; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .f-box {
    text-align: left;
    &__logo {
      text-align: center;
      flex-shrink: 0; }
    &__contact {
      flex-grow: 1; } }
  .f-contact {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      width: 220px;
      width: 31%;
      &_second {
        text-align: right; }
      &_three {
        text-align: right;
        margin-left: auto; } } }

  .f-nav-menu {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    li {
      margin-left: 8px;
      margin-right: 8px;
      margin-bottom: 5px; } }

  .f-nav-menu {
    .menu-item {
      width: auto;
      margin-top: 0;
      margin-bottom: 10px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .f-box {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    &__logo {
      margin-right: 40px;
      text-align: left; } }
  .f-nav {
    margin-top: 30px;
    .copy {
      margin-bottom: 20px; }
    &-menu {
      order: 1;
      li {
        margin-top: 0; } } }

  .f-ps-list li {
    margin: 0 10px 17px; }
  .social-item {
    margin: 0 38px 20px; }

  .f-nav-menu {
    li {
      &.active {
        a {
          &:hover {
            border-bottom-color: transparent; } } }
      a {
        &:hover {
          border-bottom-color: #494949; }
        border-bottom: 1px solid transparent; } } }

  .f-contact-box__item {
    a:hover {
      .f-contact-box__txt {
        color: #9e49ae; } } }

  /* end media min width 1000 pixels */ }



/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .f-box__contact {
    max-width: 810px;
    padding-top: 15px; }
  .f-contact {
    &__item_first {
      width: 290px; }
    &__item_second {
      width: 230px; }
    &__item_three {
      width: 180px;
      text-align: center; } }
  .f-contact-box__item {
    margin-bottom: 15px; }
  .f-nav {
    margin-top: 34px;
    padding-bottom: 7px;
    li {
      margin: 0 10px 20px; } }

  .footer-social {
    padding-top: 50px; }
  .f-ps-list li {
    margin: 0 40px 17px; }
  /* end media min width 1200 pixels */ }


/* ----- END Footer -------------------- */
