/*file*/
.jcf-file {
  position: relative;
  display: flex;
  flex-direction: column; }

// btn file
.jcf-upload-button {
  position: relative;
  display: block;

  padding-left: 30px;
  color: $cl-сadillac;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: $letspacing-standart;
  margin-bottom: 10px;
  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 7px;
    width: 13px;
    height: 13px;
    background: url(../img/icons/paper-clip.png) center center no-repeat; } }

// input file
.jcf-real-element {
  position: absolute;
  left: 0;
  top: -10px;
  height: 35px;
  width: 160px; }

// text file
.jcf-fake-input {
  order: 1;
  color: $cl-site-text;
  font-size: 12px;
  font-weight: 300;
  line-height: 14px;
  letter-spacing: $letspacing2;
  padding-left: 30px;
  display: block; }
