.picker__list {
  list-style: none;
  padding: .75em 0 4.2em;
  margin: 0; }

.picker__list-item {
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  margin-bottom: -1px;
  position: relative;
  background: #fff;
  padding: .75em 1.25em; }

.picker__list-item--highlighted, .picker__list-item:hover {
  border-color: #0089ec;
  z-index: 10; }

.picker--focused .picker__list-item--highlighted, .picker__list-item--highlighted:hover, .picker__list-item:hover {
  cursor: pointer;
  color: #000;
  background: #b1dcfb; }

@media (min-height: 46.75em) {
  .picker__list-item {
    padding: .5em 1em; } }

.picker--focused .picker__list-item--selected {
  background: #0089ec;
  color: #fff;
  z-index: 10; }

.picker__list-item--selected {
  background: #0089ec;
  color: #fff;
  z-index: 10;

  &:hover {
    background: #0089ec;
    color: #fff;
    z-index: 10; } }

.picker--focused .picker__list-item--disabled {
  background: #f5f5f5;
  color: #ddd;
  cursor: default;
  border-color: #ddd;
  z-index: auto; }

.picker__list-item--disabled {
  background: #f5f5f5;
  color: #ddd;
  cursor: default;
  border-color: #ddd;
  z-index: auto;

  &:hover {
    background: #f5f5f5;
    color: #ddd;
    cursor: default;
    border-color: #ddd;
    z-index: auto; } }

.picker--time {
  .picker__button--clear {
    display: block;
    width: 80%;
    margin: 1em auto 0;
    padding: 1em 1.25em;
    background: 0 0;
    border: 0;
    font-weight: 500;
    font-size: .67em;
    text-align: center;
    text-transform: uppercase;
    color: #666;

    &:focus, &:hover {
      background: #e20;
      border-color: #e20;
      cursor: pointer;
      color: #fff;
      outline: 0; }

    &:before {
      top: -.25em;
      color: #666;
      font-size: 1.25em;
      font-weight: 700; }

    &:focus:before, &:hover:before {
      color: #fff;
      border-color: #fff; } }

  .picker__frame {
    min-width: 256px;
    max-width: 320px; }

  .picker__box {
    font-size: 1em;
    background: #f2f2f2;
    padding: 0; } }

@media (min-height: 40.125em) {
  .picker--time .picker__box {
    margin-bottom: 5em; } }
