/* ----- Circle ------------------- */

.b-preview {
  position: relative;
  z-index: 1;
  background: url(../img/group-circle.png) center center no-repeat;
  background-size: auto 400px;
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 15px;
  padding-right: 15px;
  &-inner {
    min-height: 400px;
    padding: 40px 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__tt {
      max-width: 335px;
      margin: 0 auto;
      .title-site, h1, h2, h3, h4 {
        margin-bottom: 0; } }
    &__btn {
      margin: 40px 0;
      &-sm {
        margin-bottom: 5px; } }
    &__sub {
      margin-top: 40px;
      color: #898989;
      font-weight: 500;
      letter-spacing: $letspacing14; }
    &_wrapp {
      .b-preview-inner__tt {
        margin: 15px 0; }
      .b-preview-inner__sub {
        margin-top: 0; } } } }

.crcl {
  display: none;
  position: absolute;
  z-index: -1;
  left: 50%;
  &_1 {
    display: block;
    top: -50px;
    margin-left: -45px;
    >img {
      max-width: 90px; } }
  &_6 {
    display: block;
    bottom: -30px;
    margin-left: -150px;
    >img {
      max-width: 90px; } }
  &_7 {
    display: block;
    bottom: -30px;
    margin-left: 62px;
    >img {
      max-width: 90px; } } }
.crcl-box_second {
  .crcl {
    &_6 {
      bottom: -10px; }
    &_7 {
      bottom: -20px; } } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .b-preview {
    background-size: auto 500px;
    &-inner {
      min-height: 500px; } }
  .crcl {
    display: block;
    position: absolute;
    left: 50%;
    &_2 {
      margin-left: -353px;
      top: -13px; }
    &_3 {
      margin-left: 167px;
      top: 47px; }
    &_4 {
      margin-left: -283px;
      top: 289px; }
    &_5 {
      margin-left: 177px;
      top: 297px; } }
  .crcl-box_second {
    .crcl {
      &_2 {
        margin-left: -243px;
        top: 47px; }
      &_4 {
        max-width: 150px; }
      &_3 {
        left: auto;
        right: 50%;
        margin-left: 0;
        margin-right: -280px;
        max-width: 150px; }
      &_5 {
        left: auto;
        right: 50%;
        margin-left: 0;
        margin-right: -280px;
        max-width: 150px; }
      &_6 {
        bottom: -10px; }
      &_7 {
        bottom: -20px; } } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .b-preview {
    background-size: auto 930px;
    &-inner {
      min-height: 930px; } }
  .crcl {
    >img {
      max-width: none; }
    &_1 {
      top: -60px;
      margin-left: -85px; }
    &_2 {
      margin-left: -437px;
      top: 73px; }
    &_3 {
      margin-left: 0;
      margin-right: -473px;
      left: auto;
      right: 50%;
      top: 62px; }
    &_4 {
      margin-left: -504px;
      top: 477px; }
    &_5 {
      margin-left: 0px;
      left: auto;
      right: 50%;
      margin-right: -522px;
      top: 480px; }
    &_6 {
      margin-left: -345px;
      bottom: 27px; }
    &_7 {
      margin-left: 0px;
      left: auto;
      right: 50%;
      margin-right: -289px;
      bottom: -46px; } }

  .b-preview-inner__btn {
    margin: 130px 0; }
  .b-preview-inner__sub {
    margin-top: 30px;
    margin-bottom: 25px;
    font-size: 20px;
    line-height: 20px; }
  .crcl-box_second {
    .crcl {
      &_1 {
        top: -63px;
        margin-left: -71px; }
      &_2 {
        margin-left: -383px;
        top: 121px; }
      &_4 {
        max-width: none;
        margin-left: -492px;
        top: 449px; }
      &_3 {
        top: 84px;
        margin-right: -440px;
        max-width: none; }
      &_5 {
        max-width: none;
        top: 520px;
        margin-right: -490px; }
      &_6 {
        margin-left: -338px;
        bottom: 37px; }
      &_7 {
        bottom: -45px; } } }
  .b-preview-inner_wrapp {
    padding-bottom: 50px;
    .b-preview-inner__tt {
      max-width: 425px;
      margin: 30px 0 22px; }
    .b-preview-inner__sub {
      margin-bottom: 0; } }

  .b-preview {
    &-inner {
      &__btn {
        &-sm {
          margin-bottom: 40px; } } } }
  /* end media min width 768 pixels */ }

/* ----- END Circle --------------- */
