/* ----- Typography -------------------- */
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: $site-fill-color;
  font: $site-font-style $site-font-weight $body-text-size $site-font-family;
  color: $cl-site-text;
  letter-spacing: $letspacing-standart; }


.title-site, h1, h2, h3, h4, h5, h6 {
  display: block;
  margin: 0 0 15px;
  padding: 0;
  font-family: $title-font-weight;
  font-weight: $title-font-family;
  line-height: $title-line-height;
  color: $site-title-color;
  letter-spacing: $letspacing15; }

h1, .title-site_h1, .title-site_h1_big {
  font-size: $site-h1-size-mobile;
  margin-bottom: 20px; }

h2, .title-site_h2 {
  font-size: $site-h2-size-mobile;
  margin-bottom: 20px; }

h3, .title-site_h3 {
  font-size: $site-h3-size-mobile; }

h4, .title-site_h4 {
  font-size: $site-h4-size-mobile; }

h5, .title-site_h5 {
  font-size: $site-h5-size-mobile;
  line-height: 1.3em; }

h6, .title-site_h6 {
  font-size: $site-h6-size-mobile;
  line-height: 1.3em; }

.title-site_light {
  color: #fff; }

.title-site_font-second {
  font-family: $site-font-family-2;
  font-weight: 700; }

.title-site_push-lg {
  margin-bottom: 30px; }

ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0; }

p {
  margin: 0 0 20px; }

.text-bl {
  &_pushes {
    padding: 40px 0; }
  ul {
    margin-bottom: 20px;
    list-style-type: disc;
    padding-left: 15px; }

  ol {
    margin-bottom: 20px;
    list-style-type: decimal;
    padding-left: 15px; }

  li {
    margin-bottom: 10px; } }

.text_center {
  text-align: center; }

.text_left {
  text-align: left; }

.text_right {
  text-align: right; }

.text_uppercase {
  text-transform: uppercase; }


img {
  vertical-align: middle;
  max-width: 100%; }

a {
  color: $color-link;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
    color: $color-h-link; } }


.ff-second {
  font-family: $site-font-family-2; }

.ff-three {
  font-family: $site-font-family-3; }

.ff-four {
  font-family: $site-font-family-4; }

.font-light {
  font-weight: 300; }

.font-st {
  font-weight: normal; }

.font-medium {
  font-weight: 500; }

.font-bold {
  font-weight: 700; }

.text-st {
  font-size: 16px;
  line-height: 24px; }

.text-st_sm {
  line-height: 20px; }

.text-md {
  font-size: 18px;
  line-height: 28px; }

.cl-rose {
  color: $cl-сadillac; }

.main-title {
  position: relative;
  margin-bottom: 30px;
  &_push-lg {
    margin-bottom: 40px; }
  &__dublicate {
    display: none; }
  &__value {
    color: $cl-dusty-gray;
    font-family: $site-font-family-4;
    font-size: 40px;
    margin-right: 10px;
    font-weight: normal;
    line-height: 1;
    letter-spacing: $letspacing18; }
  .title-site, h1, h2, h3 {
    margin-bottom: 10px;
    position: relative; } }

.max-490 {
  max-width: 490px; }

.tt-cap {
  margin-bottom: 40px;
  &_push {
    margin-bottom: 50px; }
  &_push-sm {
    margin-bottom: 30px; }
  .title-site, h1, h2, h3 {
    margin-bottom: 15px; }
  .title-site_push-md {
    margin-bottom: 25px; }

  p {
    font-weight: 300;
    line-height: 20px;
    letter-spacing: $letspacing7; } }

.line-bt {
  border-bottom: 1px solid $cl-silver; }

.group-tt {
  border-bottom: 1px solid $cl-silver;
  padding-bottom: 20px;
  margin-bottom: 30px;
  &__link {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: $letspacing5;
    text-align: right;
    a {
      color: $cl-сadillac;
      text-decoration: none;
      &:hover {
        color: $cl-сadillac;
        text-decoration: underline; } } } }
.inner-box {
  margin-bottom: 15px; }
.inner-box__content {
  padding: 0 0 1px;
  margin-bottom: 17px; }

.inner-content {
  padding: 40px 0 10px;
  p {
    margin-bottom: 30px; }
  >img {
    margin-bottom: 20px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  h1, .title-site_h1 {
    font-size: $site-h1-size; }

  .title-site_h1_big {
    font-size: 36px;
    letter-spacing: $letspacing4;
    &2 {
      font-size: 48px;
      letter-spacing: $letspacing17; }
    &3 {
      font-size: 40px;
      letter-spacing: 1px; } }

  h2, .title-site_h2 {
    font-size: $site-h2-size; }

  h3, .title-site_h3 {
    font-size: $site-h3-size; }

  h4, .title-site_h4 {
    font-size: $site-h4-size; }

  h5, .title-site_h5 {
    font-size: $site-h5-size; }

  h6, .title-site_h6 {
    font-size: $site-h6-size; }

  .title-site_push-lg {
    margin-bottom: 40px; }

  .text-bl-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48%; } }

  .main-title {
    margin-bottom: 30px;
    padding-left: 80px;
    padding-top: 30px;
    z-index: 1;
    &_push-lg {
      margin-bottom: 60px; }
    &__dublicate {
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      color: #f4f4f4;
      font-size: 40px;
      font-weight: 900;
      line-height: 1;
      letter-spacing: 18px;
      z-index: -1;
      &_center-tt {
        top: 30px;
        transform: translateY(0); }
      &_sm4 {
        letter-spacing: 3.6px; } }
    &__value {
      position: absolute;
      right: 100%;
      bottom: -4px;
      font-size: 60px; }
    .title-site, h1, h2, h3 {
      margin-bottom: 14px;
      display: inline-block; }
    &_right {
      text-align: right;
      .main-title__dublicate {
        right: auto;
        left: 0; } } }

  .title-site_push-md {
    margin-bottom: 25px; }

  .tt-cap_push-md {
    margin-bottom: 70px; }

  .group-tt {
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    margin-bottom: 45px;
    &__link {
      flex-shrink: 0;
      margin-left: 20px; }
    h5, .title-site, h1, h2, h3, h4 {
      margin-bottom: 0px; } }

  .inner-content {
    padding: 60px 0 0;
    p {
      max-width: 980px;
      margin: 0 auto 40px; } }

  .title-site_h1_line-height-md {
    line-height: 48px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-title {
    &_push-lg {
      margin-bottom: 70px;
      &_big {
        margin-bottom: 90px; } }

    &_push-md {
      margin-bottom: 50px; }
    &__dublicate {
      font-size: 90px;
      &_sm {
        font-size: 72px;
        letter-spacing: 7.2px;
        &4 {
          font-size: 72px;
          letter-spacing: 3.6px; }
        &2 {
          font-size: 80px;
          letter-spacing: 8px; }
        &3 {
          font-size: 60px;
          letter-spacing: 6px; } }

      &_center-tt {
        top: 10px; } } }

  .title-site_size-md {
    font-size: 32px;
    .big-text {
      font-size: 36px;
      line-height: 1.5; } }

  .title-site_push-lg {
    margin-bottom: 50px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-title {
    padding-left: 150px;
    &_right_push {
      padding-right: 75px;
      &-sm {
        padding-right: 25px; } }
    &_push-left {
      padding-left: 180px; } }

  /* end media min width 1200 pixels */ }


/* Min width 1300 pixels */
@media only screen and (min-width: 1300px) {
  .main-title__dublicate {
    right: -30px; }
  /* end media min width 1300 pixels */ }

/* ----- END Typography ---------------- */
