/* ----- Tabs-slider ------------------- */
.box-personal {
  padding: 30px 0; }

.tabs-slider-wrapp {}

.tabs-slider-item {
  text-align: center;

  &__name {
    color: $cl-site-text;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: $letspacing17;
    margin-bottom: 10px; }
  &__txt {
    color: #555;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: 1.05px;
    min-height: 40px; } }

.tabs-slider-item__img {
  position: relative;
  width: 162px;
  height: 162px;
  border-radius: 50%;
  overflow: hidden;
  text-align: center;
  line-height: 162px;
  margin: 0 auto 25px;
  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 100%; } }

.icon-txt {
  display: flex;
  justify-content: flex-start;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  .icon {
    margin-right: 10px;
    flex-shrink: 0; }
  &__sm-text {
    font-weight: 300; }
  &__st-text {
    font-weight: 400; } }


.box-personal-icons {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  &__item {
    margin-right: 15px; } }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .tabs-slider-item {
    padding: 0 0px; }
  .tabs-slider {
    &-item {
      padding-top: 48px; }
    .slick-center {
      &.tabs-slider {
        &-item {
          padding-top: 0; } }
      .tabs-slider-item__img {
        width: 246px;
        height: 246px;
        line-height: 246px; } } }
  .box-personal-icons__item {
    margin-right: 30px; }
  .box-personal-icons {
    margin-bottom: 12px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .tabs-section_max-width {
    max-width: 910px;
    margin: 0 auto; }
  .box-personal {
    max-width: 840px;
    margin: 0 auto; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .tabs-slider {
    .slick-prev, .slick-next {
      margin-top: -70px; }
    .slick-prev {
      left: -84px; }
    .slick-next {
      right: -84px; } }
  /* end media min width 1200 pixels */ }

/* ----- END Tabs-slider --------------- */
