/* ----- Carousel-boxes ------------------- */

.s-card {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  background-color: $cl-light;
  overflow: hidden;
  &__img {
    position: relative;
    min-height: 250px;
    overflow: hidden;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: none; }

    &_mob-wrapp {
      margin-left: -10px;
      margin-right: -30px; } } }


.s-card-info {
  position: relative;
  padding-top: 50px;
  padding: 70px 15px 20px;
  text-align: center;
  &__value {
    position: absolute;
    background-image: $gradient-st;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: $shadow3;
    color: #3e3e3e;
    font-size: 56px;
    line-height: 1;
    font-weight: 900;
    letter-spacing: 2.25px;
    top: 7px;
    right: 8px; }
  &__tt {
    margin-bottom: 30px;
    .title-site, h1, h2, h3 {
      margin-bottom: 0; }
    .txt-lg {
      letter-spacing: $letspacing13;
      font-size: 24px;
      font-weight: 500;
      line-height: 1.1; } } }

.main-carousel-wrapp {
  margin-left: -15px;
  margin-right: -15px;
  .main-carousel .owl-item {
    padding: 20px 20px; } }

.main-carousel-item {
  padding: 20px; }

//slider-gallery
.slider-gallery {
  margin-bottom: 30px;
  .slick-prev {
    left: -15px; }
  .slick-next {
    right: -15px; }
  &__item {
    text-align: center;
    > img {
      margin: 0 auto; } } }

.slider-gallery-thumb {
  margin-right: -10px;
  &__wrapp-img {
    text-align: center;
    margin-right: 10px;
    overflow: hidden;
    position: relative;
    height: 65px;
    display: block;
    text-decoration: none;
    outline: none;
    border-radius: 4px;

    >img {
      width: 100%;
      height: 100%;
      object-fit: cover; } } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .slider-gallery-thumb {
    &__wrapp-img {
      height: 105px; } }
  /* end media min width 480 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .s-card {
    display: flex;
    justify-content: space-between;
    &__img {
      flex-shrink: 0;
      flex-basis: 400px;
      min-width: 400px;
      max-width: 400px;
      margin-left: 0;
      margin-right: 0;
      >img {
        left: auto;
        right: 0;
        transform: translate(0, -50%); } }
    &-info {
      text-align: left;
      padding-bottom: 50px;
      flex-grow: 1;
      &__value {
        font-size: 78px; } } }

  .slider-gallery-thumb {
    &__wrapp-img {
      height: 175px; } }

  .slider-gallery {
    margin: 0 100px 20px;
    .slick-prev {
      left: -75px; }
    .slick-next {
      right: -75px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-carousel-wrapp {
    max-width: 1010px;
    margin: auto; }
  .s-card {
    &__img {
      flex-basis: 460px;
      min-width: 460px;
      max-width: 460px; }
    &-info {
      &__value {
        font-size: 90px;
        top: 29px;
        right: 37px; } } }

  .s-card-info__tt .title-site, .s-card-info__tt h1, .s-card-info__tt h2, .s-card-info__tt h3 {
    font-size: 48px;
    letter-spacing: $letspacing17; }
  .s-card-info__tt .txt-lg {
    font-size: 30px; }
  .s-card-info {
    padding-left: 38px; }
  .s-card-info__btn {
    margin-top: 34px; }

  .slider-gallery-thumb {
    margin-left: 0;
    margin-right: 0;
    padding-left: 10px;
    padding-right: 10px;
    &__item {
      margin-right: 15px;
      margin-left: 15px; }
    &__wrapp-img {
      height: 251px; } }

  .slider-gallery {
    margin: 0 150px 55px;
    .slick-prev {
      left: -95px; }
    .slick-next {
      right: -95px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-gallery {
    margin-left: 198px;
    margin-right: 198px;
    .slick-prev {
      left: -125px; }
    .slick-next {
      right: -125px; } }

  .slider-gallery-thumb {
    &__wrapp-img {
      >img {
        max-width: none; } } }
  /* end media min width 1200 pixels */ }

/* ----- END Carousel-boxes --------------- */
