/* ----- Bg Index ------------------- */
/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .bg-content {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      top: 0;
      right: 0;
      left: 0;
      background: url(../img/bg-page.jpg) center top no-repeat;
      background-size: 1900px 100%;
      z-index: -1; } }
  /* end media min width 1000 pixels */ }

/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .bg-content {
    &:before {
      background-size: cover; } }
  /* end media min width 1900 pixels */ }

/* ----- END Bg Index --------------- */
