/* ----- Header ------------------------ */
.header {
  position: relative;
  padding: 10px 0; }

.logo {
  a {
    display: block;
    outline: none; } }

.head-soc {
  padding-top: 58px;
  text-align: right; }

.social-sm {
  li {
    display: inline-block;
    vertical-align: middle;
    margin-left: 11px; }
  a {
    &:hover {
      opacity: 0.6; } } }

.logo_header {
  float: left;
  max-width: 150px; }

.head-soc-btn {
  display: none; }

.head-soc-phone {
  margin-top: 10px;
  &__point {
    display: flex;
    justify-content: flex-end;
    color: #252525;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.35px;
    text-decoration: none;
    outline: none;
    &:hover {
      text-decoration: none;
      color: #252525; } }
  .icon-second, .icon, .icon-s {
    flex-shrink: 0;
    &_two {
      margin-left: 10px; }
    &_message {
      position: relative;
      top: 2px; } }
  &__txt {
    flex-shrink: 0;
    margin-left: 14px; } }

a.head-soc-phone__point {
  &:hover {
    .head-soc-phone__txt {
      color: $cl-site-hover; } } }

.head-ard {
  display: none; }



/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .head-soc-phone-box {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    width: 100%;
    .head-soc-phone {
      flex-shrink: 0;
      margin-left: 30px; } }

  /* end media min width 480 pixels */ }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .head-soc {
    margin-left: 180px;
    padding-top: 8px;
    text-align: right;
    margin-right: 70px; }

  .head-ard {
    text-align: right;
    color: $cl-site-text;
    font-size: 12px;
    font-weight: 300;
    line-height: 20px;
    margin-left: 180px;
    margin-right: 70px;
    display: block;
    &__inner {
      text-decoration: none;
      color: $cl-site-text;
      padding-left: 26px;
      position: relative;
      .icon-s, .icon {
        position: absolute;
        top: -4px;
        left: 0; }
      &:hover {
        color: $cl-site-hover;
        text-decoration: none; } } }

  .head-soc-phone-box .head-soc-phone {
    margin-left: 15px;
    &:first-child {
      margin-left: 0; } }

  .head-soc-phone__point {
    font-size: 13px; }


  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .head-ard {
    font-size: 14px;
    margin-right: 0;
    margin-left: 240px;
    &__inner {
      .icon-s, .icon {
        top: -2px; } } }
  .head-soc-phone__point {
    font-size: 14px; }
  .header {
    padding: 15px 0 15px; }
  .logo_header {
    position: relative;
    z-index: 10; }
  .head-soc {
    max-width: 500px;
    margin-top: 30px;
    padding-top: 0;
    margin-right: 0;
    margin-left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    .head-soc__contact {
      width: 190px; } }
  .head-soc-phone-box {
    display: block; }
  .head-soc-btn {
    display: block;
    padding-left: 15px;
    margin-left: 19px;
    margin-top: -23px;
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 1px;
      background-color: #e7dbeb; }
    &__item {
      text-align: center; } }
  .head-soc-btn__item_first {
    padding-bottom: 7px;
    margin-bottom: 7px;
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      right: -15px;
      left: -15px;
      height: 1px;
      background-color: #e7dbeb; } }
  .head-soc-phone-box {
    margin-top: 10px; }
  .head-soc-phone {
    margin-top: 5px; }


  //Sticky Header
  #wrapper.sticky {
    padding-top: 251px;
    .header {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 999;
      background-color: #fff;
      box-shadow: 0 0 5px rgba(#000, 0.3);
      padding: 1px 0;
      animation: ease-in-out header-sticky 0.4s both;

      .logo_header {
        max-width: 90px;
        margin-left: 0; }
      .head-ard, .head-soc {
        display: none; }
      .menu {
        padding-top: 15px;
        padding-bottom: 15px;
        margin-left: 0;
        justify-content: center;
        width: 100%; }
      .head-menu {
        margin-left: 85px;
        margin-right: 85px; } } }

  @keyframes header-sticky {
    0% {
      top: -54px; }
    100% {
      top: 0; } }


  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .header {
    padding: 10px 0; }
  .logo_header {
    max-width: none;
    margin-left: -25px; }
  .head-soc {
    margin-top: 75px;
    max-width: 540px; }
  .head-soc .head-soc__contact {
    width: 269px;
    margin-top: -12px;
    position: relative;
    z-index: 10; }

  .head-soc-phone {
    margin-top: 5px; }
  .social-sm {
    margin-bottom: 10px; }
  .head-soc-btn {
    margin-left: 35px;
    margin-top: -38px;
    padding-left: 34px; }
  .head-soc-btn__item_first:before {
    left: -34px;
    right: -23px; }
  .head-soc-btn:before {
    bottom: 0; }
  .social-sm li {
    margin-left: 13px; }
  .head-soc-phone-box {
    margin-top: 13px; }
  .head-soc-phone {
    margin-top: 3px; }

  /* end media min width 1200 pixels */ }

/* ----- END Header -------------------- */
