
/* Slider */
/* Navigation */
$owl-nav-wh: 40px;
$owl-nav-arr-w: 8;
$owl-nav-arr-h: 12;
$owl-nav-arr-left: url(../img/icons/arrow-left-slider.svg);
$owl-nav-arr-right: url(../img/icons/arrow-right-slider.svg);

.slick-slide {
  outline: none; }
// arrow

.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  border: none;
  outline: none;
  background: transparent;

  position: absolute;
  top: 50%;
  width: $owl-nav-wh;
  height: $owl-nav-wh;
  margin: 0;
  margin-top: -($owl-nav-wh/2);
  background-color: $cl-light;
  padding: 0;
  box-shadow: $shadow2;
  border-radius: 50%;
  z-index: 1; }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }



.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }

.slick-prev:before, .slick-next:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background-position: 0 0;
  background-repeat: no-repeat;

  width: $owl-nav-arr-w+(px);
  height: $owl-nav-arr-h+(px);
  margin-top: -($owl-nav-arr-h/2)+(px); }

.slick-prev {
  left: 0;
  &:before {
    margin-left: -($owl-nav-arr-w/2+1)+(px);
    background-image: $owl-nav-arr-left;
    background-size: 100% 100%; } }

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto; }

.slick-next {
  right: 0;
  &:before {
    margin-left: -($owl-nav-arr-w/2-1)+(px);
    background-image: $owl-nav-arr-right;
    background-size: 100% 100%; } }

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px; }


.dots-slider-sm {
  .slick-prev, .slick-next {
    background-color: transparent;
    box-shadow: none;
    border-radius: 0px;
    &:before {
      width: 8px;
      margin-top: -10px;
      height: 20px; } }
  .slick-prev {
    left: -15px;
    &:before {
      background-image: url(../img/icons/arrow-left-slider-sm.svg); } }
  .slick-next {
    right: -15px;
    &:before {
      background-image: url(../img/icons/arrow-right-slider-sm.svg); } } }



/* Dots */
$owl-dot-wh: 12;
$owl-dot-margin: 6;
$wh-dots: $owl-dot-wh+($owl-dot-margin*2);

.slick-dots {
  position: absolute;
  bottom: -30px;
  width: 100%;
  text-align: center;

  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 $owl-dot-margin+(px);
    padding: 0;
    cursor: pointer;

    button {
      font-size: 0;
      line-height: 0;
      display: inline-block;
      position: relative;
      width: $owl-dot-wh+(px);
      height: $owl-dot-wh+(px);
      padding: 0;
      margin: 0;
      border-radius: 50%;
      background-color: lighten(#000, 50%);
      background-clip: content-box;
      border: 2px solid transparent;
      outline: none;


      &:hover:before, &:focus:before {
        opacity: 1; }

      &:before {
        content: ' ';
        position: absolute;
        top: 50%;
        left: 50%;
        width: $wh-dots+(px);
        height: $wh-dots+(px);
        margin-top: -($wh-dots/2)+(px);
        margin-left: -($wh-dots/2)+(px);
        border-radius: 50%;
        background-color: transparent; } }


    &.slick-active button {
      width: $owl-dot-wh+(px);
      height: $owl-dot-wh+(px);
      background-color: #000;
      border: 2px solid $cl-light;
      background-image: $gradient-st;
      box-shadow: 0 0 6px rgba(17, 17, 17, 0.6); } } }

.slider-dots-big {
  .slick-dots {
    li {
      button {
        width: 14px;
        height: 14px;
        box-shadow: inset 0 0 0 2px $cl-silver;
        background-color: #fff;
        border: 2px solid transparent; }

      &.slick-active button {
        background-color: #000;
        border: none;
        background-image: $gradient-st;
        box-shadow: none; } } } }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slick-prev, .slick-next {
    width: 60px;
    height: 60px;
    margin-top: -30px;
    &:hover {
      box-shadow: $shadow2-hover; } }
  .slick-prev {
    left: -20px; }
  .slick-next {
    right: -20px; }
  .slick-dots {
    bottom: -43px; }
  .owl-carousel-wrapp-dots {
    padding-bottom: 55px; }
  .dots-slider-sm {
    .slick-prev, .slick-next {
      box-shadow: none; } }
  .slider-dots-big {
    .slick-dots {
      bottom: -52px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slick-prev {
    left: -80px; }
  .slick-next {
    right: -80px; }
  /* end media min width 1200 pixels */ }
