/* ----- Price Form ------------------- */
.main-price-images {
  display: none; }

.img-inner-border {
  &_circle {
    overflow: hidden;
    border-radius: 50%;
    max-width: 280px;
    min-height: 280px;
    &:before {
      border-radius: 50%; } } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .main-price-wrapp__inner {
    position: relative; }
  .main-price-images {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__left {
      width: 50%;
      .img-inner-border {
        min-height: 400px; }
      .img-inner-border > img {
        left: 0;
        transform: translateY(-50%);
        max-width: 120%; } }
    &__right {
      width: 50%;
      text-align: right;
      .img-inner-border > img {
        max-width: 120%; } } }

  .img-inner-border {
    &_circle {
      max-width: 320px;
      min-height: 320px; } }

  .main-price__form {
    .form-head {
      margin-left: auto;
      margin-right: auto; } }

  .main-price-wrapp__inner {
    position: relative;
    z-index: 1;
    .main-price-images {
      position: absolute;
      top: 50%;
      transform: translateY(-43%);
      left: 0;
      right: 0;
      z-index: -1; } }


  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-price-images__left .img-inner-border {
    min-height: 550px;
    > img {
      max-width: none; } }
  .img-inner-border_circle {
    max-width: 486px;
    min-height: 486px; }
  .main-price-images__left {
    flex-grow: 1;
    width: auto; }
  .main-price-images__right {
    flex-shrink: 0;
    max-width: 486px;
    margin-left: 30px; }

  .main-price-wrapp {
    .title-site, h1, h2, h3 {
      margin-bottom: 50px; } }
  /* end media min width 1000 pixels */ }


/* ----- END Price Form --------------- */
