/* ----- Navigation -------------------- */
/* -- Bread crumbs ------------------ */
.bread-crumbs {
  padding: 0;
  list-style-type: none;
  display: none;

  @include tabl {
    display: block; }

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    font-size: 12px;
    color: #252525;
    font-family: $site-font-family-2;
    font-weight: 400;
    letter-spacing: 0.3px;
    a {
      font-family: $site-font-family-2;
      font-weight: 300;
      color: #252525;
      text-decoration: none;
      &:hover {
        text-decoration: underline; } }

    &:after {
      content: '\\';
      margin: 0 1px 0 5px; }

    &:last-child {
      &:after {
        display: none; } } } }

.banner-bread-crumbs {
  .bread-crumbs {
    li {
      color: $cl-site-text-second;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: $letspacing7;
      font-weight: 500;
      a {
        font-weight: 300;
        color: $cl-site-text-second; } } } }

/* -- Pagination -------------------- */
.pagination {
  list-style-type: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  li {
    display: inline-block;
    vertical-align: middle;
    margin: 10px 0px;
    color: #737373;
    font-size: 14px;
    &:last-child {
      a {
        border-right: 1px solid #e5e5e5; } } }

  a, li > span {
    display: block;
    width: 28px;
    height: 28px;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-right: none;
    line-height: 28px;
    text-align: center;
    text-decoration: none;
    color: #737373; }
  a {
    &:hover {
      color: #fff;
      background-image: $gradient-st;
      border-color: #e877b7;
      border: none;
      background-clip: border-box; } }

  &__active {
    a {
      border: none;
      background-color: #fc8a00;
      color: #fff;
      background-image: $gradient-st; } } }

.pagination_push {
  margin-top: 60px;
  &_sm {
    margin-top: 45px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .pagination {
    a, li > span {
      width: 36px;
      height: 36px;
      line-height: 36px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .pagination_push {
    margin-top: 90px; }
  /* end media min width 1000 pixels */ }


/* -- Main menu --------------------- */
.menu-open-wrapper-page {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0; }

.burger-menu {
  display: block;
  position: absolute;
  right: 5px;
  top: 20px;
  z-index: 101;
  cursor: pointer;
  padding: 10px; }

.burger-menu-box {
  display: block;
  width: 30px;
  padding: 8px 0; }

.burger-menu-inner {
  display: block;
  position: relative;
  height: 3px;
  background-color: $burger-fill;
  border-radius: 3px;
  transition: transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);

  &:before,
  &:after {
    content: ' ';
    position: absolute;
    left: 0;
    height: 3px;
    background-color: $burger-fill;
    width: 100%;
    border-radius: 3px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform; }

  &:before {
    top: -8px;
    transition: top 75ms ease .12s, opacity 75ms ease; }

  &:after {
    bottom: -8px;
    transition: bottom 75ms ease .12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19); } }

.menu-burger--opened {
  .burger-menu {
    position: fixed; }

  .burger-menu-inner {
    transition-delay: 0.12s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: rotate(45deg);

    &:before {
      top: 0;
      transition: top 75ms ease, opacity 75ms ease 0.12s;
      opacity: 0; }

    &:after {
      bottom: 0;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) .12s;
      transform: rotate(-90deg); } }

  .burger-menu-inner, .burger-menu-inner:after, .burger-menu-inner:before {
 } }    // background-color: $burger-fill-open

.menu-wrapper-fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: left 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000); }

.menu-open-wrapper {
  padding: 20px;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transition: background 0.25s ease-out; }

.menu-burger--opened {
  .menu-wrapper-fixed {
    left: 0; }

  .menu-open-wrapper {
    background-color: $cl-alabaster2; }

  .menu-item {
    margin-left: 0;
    @for $i from 1 through 20 {
      &:nth-child(#{$i}) {
        transition-delay: (0.06s * $i) - 0.06s; } } } }

.header-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100%; }

.menu {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu-item {
  position: relative;
  margin-top: 13px;
  margin-bottom: 13px;
  padding: 0;
  list-style-type: none;
  font: $font-menu-item-mobile;
  color: $cl-site-text-second;
  width: 100%;
  margin-left: -100%;
  transition: all 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000);
  &_active {
    .menu-item__link {
      color: #9e49ae;
      border-bottom-color: #151515;
      cursor: default;
      font-weight: 500; } }

  &__link {
    color: $cl-site-text-second;
    text-decoration: none;
    border-bottom: 1px solid transparent;

    &:hover {
      color: #9e49ae;
      border-bottom-color: #151515; } } }

.menu-item_mobile {
  .icon-s_houzz {
    transform: scale(1.2);
    position: relative;
    top: -2px;
    margin-left: 8px;
    margin-right: 8px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .burger-menu {
    top: 30px; }
  .menu-item {
    font-size: 20px;
    line-height: 22px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .menu-burger--opened {
    .menu-open-wrapper {
      background-color: transparent; } }
  .head-menu {
    margin-left: 150px; }
  .menu-open-wrapper-page {
    overflow: visible;
    position: relative;
    height: auto;
    width: auto; }

  .menu-wrapper-fixed {
    position: relative;
    left: 0;
    top: 0;
    height: auto;
    transition: none;
    width: 100%;
    z-index: 5; }

  .menu-open-wrapper {
    background: none;
    padding: 0;
    height: auto;
    overflow: visible; }

  .burger-menu {
    display: none; }

  .header-nav {
    display: block; }

  .menu {
    text-align: right;
    border-radius: 5px;
    padding-top: 5px;
    padding-left: 10px;
    position: relative;
    padding-bottom: 18px;
    // &:before, &:after
    //   content: ' '
    //   position: absolute
    //   background-color: #e7dbeb
    // &:before
    //   top: 9px
    //   left: 0
    //   bottom: -14px
    //   width: 1px
    // &:after
    //   left: -21px
    //   bottom: 0
    //   width: 98px
 }    //   height: 1px

  .menu-item {
    display: inline-block;
    vertical-align: middle;
    font-size: 12px;
    line-height: 20px;
    font-weight: 300;
    width: auto;
    margin: 0 0 0 1px;
    &:first-child {
      margin-left: 0; }

    &__link {
      display: block;
      padding: 2px 0; } }

  .menu-item {
    border: none;
    &_active {
      .menu-item__link {
        color: #9e49ae;
        font-weight: 300;
        &:after {
          left: 0;
          visibility: visible; } } }
    &__link {
      border: none;
      position: relative;
      &:hover {
        &:after {
          visibility: visible;
          left: 0; } }
      &:after {
        content: ' ';
        position: absolute;
        bottom: 0;
        transition: ease-in-out 0.2s;
        right: 0;
        left: 100%;
        visibility: hidden;
        height: 1px;
        background-color: #151515; } } }

  .menu-item_mobile {
    display: none; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .head-menu {
    margin-left: 200px; }
  .menu-item {
    font-size: 13px;
    line-height: 18px;
    margin-left: 9px; }
  /* end media min width 1200 pixels */ }

