/* ----- Index page -------------------- */

.skills-icons {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  .skills-icon {
    width: 48%;
    margin-left: 1%;
    margin-right: 1%; } }


.skills-icon {
  text-align: center;
  margin-bottom: 30px;
  &__icon {
    width: 84px;
    line-height: 84px;
    text-align: center;
    height: 84px;
    box-shadow: 0 0 15px rgba(154, 103, 112, 0.3), 0 0 0 12px #fff, 0 0 0 13px $cl-silver2;
    border: 1px solid #e865af;
    background-color: $cl-alabaster2;
    margin: 0 auto 20px;
    border-radius: 50%; }
  p {
    color: $cl-site-text-six;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: $letspacing7; } }

// .box
//   display: none
//   &.visible
//     display: block
.box.hidden {
  display: none; }

.variant-price-bl {
  margin-top: 40px;
  margin-bottom: 40px; }

.price-card {
  display: block;
  text-decoration: none;
  outline: none;
  padding: 10px 14px;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  border: 1px solid #e5e5e5;
  background-color: $cl-light;
  position: relative;
  z-index: 1;
  text-align: center;
  margin-bottom: 15px;
  &-cap {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      top: -11px;
      left: -15px;
      right: -15px;
      height: 210px;
      border-radius: 10px;
      border: 1px solid #e5e5e5;
      background-color: #a07eaf;
      z-index: -1; }
    &__tt {
      color: $cl-light;
      font-size: 22px;
      font-weight: 500;
      line-height: 1.1;
      text-transform: uppercase;
      letter-spacing: 2.8px;
      margin-bottom: 2px; }
    &__value {
      color: $cl-light;
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: $letspacing5;
      margin-bottom: 10px; } }

  &__img {
    height: 196px;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid #e5e5e5;
    background-color: #a07eaf;
    >img {
      height: 100%;
      width: 100%;
      object-fit: cover; } }

  &__info {
    margin-top: 10px;
    li, p {
      font-size: 12px;
      font-weight: 400;
      letter-spacing: 0.38px;
      line-height: 16px;
      margin-bottom: 8px;
      b {
        font-weight: 500;
        letter-spacing: 0.38px; } } } }

a.price-card {
  &:hover,
  &:focus {
    box-shadow: 2px 0 5px rgba(#000, 0.3); } }

a.price-card__img {
  display: block;
  text-decoration: none;
  outline: none; }

.factor-box {
  outline: none;
  display: block;
  margin-bottom: 20px;
  overflow: hidden;
  &es {
    margin-bottom: 40px;
    margin-top: 30px; }
  &__img {
    flex: 0 0 70px;
    max-width: 70px;
    margin-bottom: 0px;
    width: 70px;
    height: 55px;
    float: left;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    background-color: #ccc;
    overflow: hidden;
    margin-right: 15px;
    >img {
      height: 100%;
      width: 100%;
      object-fit: cover; } }
  &__tt {
    color: $cl-site-text-second;
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    text-transform: uppercase;
    letter-spacing: $letspacing7;
    margin-bottom: 10px; }
  &__content {
    flex-grow: 1;
    li, p {
      margin-bottom: 8px;
      color: $cl-site-text-second;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.38px;
      b {
        letter-spacing: 0.38px; } } } }

a.factor-box {
  &:hover,
  &:focus {
    .factor-box__img {
      box-shadow: 2px 2px 5px rgba(#000, 0.3); } } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .price-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%;
    .price-card {
      margin-left: 1%;
      margin-right: 1%;
      flex: 0 0 48%;
      max-width: 48%; } }

  .factor-box {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    &__img {
      float: none;
      flex-basis: 120px;
      max-width: 120px;
      width: auto;
      height: 80px; } }
  /* end media min width 480 pixels */ }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .price-cards {
    align-items: flex-start;
    .price-card {
      flex: 0 0 31.3%;
      max-width: 31.3%; } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .skills-icons {
    justify-content: center;
    margin-left: -1%;
    margin-right: -1%;
    .skills-icon {
      width: 23%;
      margin-left: 1%;
      margin-right: 1%;
      margin-bottom: 10px; } }
  .skills-icon {
    &__icon {
      margin-bottom: 30px; } }

  .price-card {
    &-cap {
      &__tt {
        font-size: 28px;
        margin-bottom: 2px; }
      &__value {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 10px; } }

    &__info {
      li, p {
        font-size: 15px;
        line-height: 20px;
        margin-bottom: 5px; } } }

  .variant-price-bl {
    margin-bottom: 60px; }

  .factor-box {
    &__tt {
      font-size: 16px;
      line-height: 20px; }
    &__content {
      li, p {
        font-size: 15px;
        line-height: 22px; } } }

  .factor-box {
    margin-bottom: 30px;
    &__img {
      flex-basis: 175px;
      max-width: 175px;
      height: 110px;
      margin-right: 26px; } }

  .factor-box__content li, .factor-box__content p {
    margin-bottom: 10px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .skills-icons {
    padding-top: 10px;
    .skills-icon {
      max-width: 230px; } }
  .skills-icon {
    p {
      font-size: 16px;
      line-height: 22px; } }
  .skills-icon__icon {
    margin-bottom: 40px; }

  .price-card__info {
    margin-top: 15px; }
  .price-card__info li, .price-card__info p {
    margin-bottom: 10px; }
  .variant-price-bl {
    margin-bottom: 80px;
    margin-top: 120px; }
  .price-cards {
    margin-top: 40px; }

  .factor-boxes {
    max-width: 1000px;
    margin: 40px auto; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .price-cards {
    margin-left: -15px;
    margin-right: -15px;
    .price-card {
      flex-basis: 300px;
      max-width: 300px;
      margin-left: 15px;
      margin-right: 15px; } }
  /* end media min width 1200 pixels */ }

/* ----- END index page /--------------- */
