/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

#wrapper {
  min-width: 320px;
  overflow: hidden; }

.section-st {
  padding: 40px 0;
  &_hidden {
    overflow: hidden; }
  &_bt-sm.section-st {
    padding-bottom: 10px; } }
.container {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

.visible-lap, .hidden-full, .hidden-mobile {
  display: none; }

.section-content {
  padding: 50px 0;
  &_sm.section-content {
    padding-top: 30px; }
  &_sm_second.section-content {
    padding-top: 50px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .hidden-mobile {
    display: block; }

  .hidden-tabl {
    display: block; }

  .section-st_big.section-st {
    padding: 120px 0; }
  .section-st {
    &_bt-sm.section-st {
      padding-bottom: 10px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  html, body {
    height: 100%; }

  body {
    display: flex;
    flex-direction: column; }

  #wrapper {
    flex: 1 0 auto; }

  .footer {
    flex-shrink: 0; }

  .section-st {
    padding: 50px 0;
    &_md_second {
      padding: 60px 0; }
    &_md {
      padding: 70px 0; }
    &_push-bt-non.section-st {
      padding-bottom: 0; }
    &_bt-md.section-st {
      padding-bottom: 30px; }
    &_push-bt-lg.section-st {
      padding-bottom: 60px; } }


  .container {
    max-width: 980px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 auto; }

  .visible-lap {
    display: block; }

  .hidden-lap {
    display: none; }

  .section-content {
    padding: 100px 0 45px;
    &_md.section-content {
      padding-top: 70px; }
    &_second {
      padding-top: 80px;
      padding-bottom: 20px; } }

  .block-grid__left {
    margin-bottom: 20px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .section-st_lg {
    padding: 80px 0; }
  .hidden-lap {
    display: block; }
  .container {
    max-width: 1190px; }
  .section-st_big.section-st {
    padding-top: 153px; }
  .section-st.section-st_push-bt-md {
    padding-bottom: 70px; }
  .section-st.section-st_push-bt-lg {
    padding-bottom: 100px; }
  /* end media min width 1200 pixels */ }

























