/* select */

body > .jcf-select-drop {
  position: absolute;
  margin: -1px 0 0;
  z-index: 9999;
  &.jcf-drop-flipped {
    margin: 1px 0 0; } }

.jcf-select {
  .jcf-select-drop {
    position: absolute;
    margin-top: 0px;
    z-index: 1;
    top: 100%;
    left: -1px;
    right: -1px; }
  .jcf-drop-flipped {
    bottom: 100%;
    top: auto; }
  &.jcf-compact-multiple {
    max-width: 220px;
    .jcf-select-opener:before {
      display: inline-block;
      padding-top: 2px;
      content: '...'; } } }


$select-radius: 2px;
$select-height: 33px;
$select-fill-color: $cl-wild-sand;
$select-border: 1px solid $cl-alto;

$arrow-width: 20px;
$arrow-height: 20px;
$arrow-url: url(../img/icon/arrow-down.png);
$arrow-transition: ease-in-out 0.2s;

$padding-goriz: 16px;
$padding-vert: 16px;
$background-item: #e6e6e6;
$color-item: #000;
$color-item-hover: #000;

@keyframes select-anim {
  0% {
    opacity: 0;
    transform: rotateX(-90deg); }
  100% {
    opacity: 1;
    transform: rotateX(0deg); } }


.jcf-select {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  min-width: 173px;
  flex-shrink: 0;
  background: $select-fill-color;
  height: $select-height;
  border-radius: $select-radius;
  border: $select-border;
  perspective: 1000px;
  z-index: auto;
  select {
    z-index: 1;
    left: 0;
    top: 0; }
  //text top
  .jcf-select-text {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    cursor: default;
    display: block;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: $letspacing-standart;
    color: #141414;
    line-height: $select-height;
    margin: 0 35px 0 $padding-vert; }
  // arrow top
  .jcf-select-opener {
    position: absolute;
    bottom: 0;
    right: 0;
    top: 0;
    text-align: center;
    width: 29px;
    line-height: $select-height;
    border-left: 1px solid $cl-alto;
    border-radius: 2px;
    &:before {
      content: "\f078";
      display: inline-block;
      font: normal normal normal 14px/1 FontAwesome;
      font-size: inherit;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      font-weight: normal;
      font-style: normal;
      color: $cl-site-text-three;

      position: absolute;
      top: 50%;
      left: 50%;
      transition: $arrow-transition;
      font-size: 9px;
      margin-top: -(8px/2);
      margin-left: -(10px/2); } } }

.jcf-drop-active {
  border-radius: $select-radius $select-radius 0 0;
  z-index: 99;
  // arrow top active
  .jcf-select-opener {
    &:before {
      transform: rotate(180deg); } }
  .jcf-select-drop {
    z-index: 99; } }

.jcf-drop-flipped.jcf-select {
  border-radius: 0 0 $select-radius $select-radius;
  .jcf-select-opener {
    &:before {
      transform: rotate(0deg); } } }

.jcf-drop-flipped.jcf-select-drop {
  border-radius: $select-radius $select-radius 0 0;
  transform-origin: bottom left;
  transform: none;
  animation: none;
  .jcf-select-drop-content {
    border: $select-border;
    border-radius: $select-radius $select-radius 0 0;
    overflow: hidden; } }

//box bottom
.jcf-select-drop {
  transition: ease 0.3s;
  animation: {
    name: select-anim;
    duration: 300ms;
    timing-function: ease-in-out; }
  transform-origin: top center;
  //border box bottom
  .jcf-select-drop-content {
    border: $select-border;
    border-radius: 0 0 $select-radius $select-radius;
    overflow: hidden; }
  &.jcf-compact-multiple {
    .jcf-hover {
      background: none; }
    .jcf-selected {
      background: #e6e6e6;
      color: #000;
      &:before {
        display: inline-block;
        content: '';
        height: 4px;
        width: 8px;
        margin: -7px 5px 0 -3px;
        border: 3px solid #777;
        border-width: 0 0 3px 3px;
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0.7071067811865483, M12=0.7071067811865467, M21=-0.7071067811865467, M22=0.7071067811865483, SizingMethod='auto expand')"; } } } }

/* multiple select styles */

.jcf-list-box {
  overflow: hidden;
  display: inline-block;
  border: 1px solid #b8c3c9;
  min-width: 200px;
  margin: 0 15px; }

/* select options styles */

.jcf-list {
  display: inline-block;
  vertical-align: top;
  position: relative;
  background: #fff;
  line-height: 14px;
  font-size: 12px;
  width: 100%;
  .jcf-list-content {
    vertical-align: top;
    display: inline-block;
    overflow: auto;
    width: 100%;
    max-height: 100% !important; }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      overflow: hidden;
      display: block; } }
  .jcf-overflow {
    overflow: auto; }
  .jcf-option {
    white-space: nowrap;
    overflow: hidden;
    cursor: default;
    display: block;
    padding: 5px $padding-goriz;
    color: $color-item;
    min-height: 1px;
    text-decoration: none;
    text-overflow: ellipsis;
    height: auto; }
  .jcf-disabled {
    background: #fff !important;
    color: #aaa !important; } }

.jcf-select-drop .jcf-hover, .jcf-list-box .jcf-selected {
  background: $background-item;
  color: $color-item-hover; }

.jcf-list {
  .jcf-optgroup-caption {
    white-space: nowrap;
    font-weight: bold;
    display: block;
    padding: $padding-vert $padding-goriz;
    cursor: default;
    color: #000; }
  .jcf-optgroup .jcf-option {
    padding-left: 30px; } }

.jcf-select-color .jcf-select-text .jcf-option-color {
  font-weight: bold; }

.jcf-select-text .jcf-option-red, .jcf-list .jcf-option-red {
  color: #d00; }

.jcf-select-text .jcf-option-green, .jcf-list .jcf-option-green {
  color: #0d0; }

.jcf-select-text .jcf-option-blue, .jcf-list .jcf-option-blue {
  color: #00d; }

.jcf-select-drop .jcf-option-hideme {
  display: none; }

.jcf-select.jcf-select-present {
  line-height: 48px;
  height: 48px;
  background: #fff;
  .jcf-select-text img {
    vertical-align: top;
    width: 32px;
    height: auto;
    margin: 7px 5px 0 0; } }

.jcf-select-present {
  .jcf-select-text {
    line-height: 48px; }
  .jcf-list {
    font-style: italic;
    line-height: 48px;
    img {
      vertical-align: middle;
      margin: 0 5px 0 0; } } }

/* scrollbars */

.jcf-select-drop {
  .jcf-scrollable-wrapper {
    box-sizing: content-box;
    position: relative; }


  .jcf-scrollbar-vertical {
    position: absolute;
    cursor: default;
    bottom: 0;
    right: 0;
    top: 0;
    background: none;
    width: 10px;
    padding: 0 0 0 2px;

    .jcf-scrollbar-dec, .jcf-scrollbar-inc {
      display: none; } }



  .jcf-scrollbar-horizontal {
    .jcf-scrollbar-handle, .jcf-scrollbar-slider, .jcf-scrollbar-dec, .jcf-scrollbar-inc {
      display: none; }


    display: none; }


  .jcf-scrollbar-vertical .jcf-scrollbar-handle {
    height: 1px;
    width: 6px;
    border-radius: 10px;
    background: #ccc; }


  .jcf-scrollbar.jcf-inactive .jcf-scrollbar-handle {
    visibility: hidden; } }



/* end scrollbars */

.jcf-list-content .jcf-option-placeholder {
  display: none; }


.jcf-select-slct-img {
  &.jcf-select {
    min-width: 195px; }
  &.jcf-select .jcf-select-opener:before {
    left: -10px; }
  &.jcf-select .jcf-select-opener {
    width: 42px; }
  .jcf-select-text {
    > img {
      position: absolute;
      top: 2px;
      right: 2px;
      bottom: 2px;
      width: 36px;
      border-radius: 3px; } }
  .jcf-option {
    >img {
      width: 36px;
      margin-top: -14px;
      height: 27px;
      position: absolute;
      top: 50%;
      right: 2px; } }
  &.jcf-select .jcf-select-text {
    margin-right: 60px; }
  .jcf-list .jcf-option {
    position: relative;
    padding-top: 9px;
    padding-right: 43px;
    padding-bottom: 9px; } }

.jcf-select-slct-img_big {
  &.jcf-select {
    max-width: 100%;
    margin-top: 25px;
    margin-bottom: 25px; }
  &.jcf-select-slct-img .jcf-select-text > img {
    height: 84px;
    top: -26px;
    bottom: auto;
    width: 111px;
    right: -1px;
    border-radius: 4px;
    overflow: hidden;
    background-color: #2975bb; }
  &.jcf-select .jcf-select-opener {
    width: 111px;
    border: none; }
  &.jcf-select .jcf-select-text {
    margin-right: 132px; }
  .jcf-select-text {
    &:after {
      content: '';
      position: absolute;
      right: 104px;
      top: 50%;
      margin-top: -9px;
      z-index: 1;
      border-left: 6px solid #fff;
      border-top: 9px solid transparent;
      border-bottom: 9px solid transparent; } }
  &.jcf-drop-active {
    .jcf-select-text > img {
      height: 27px;
      top: 2px;
      bottom: 2px;
      right: 2px;
      width: 36px;
      border-radius: 1px; }
    &.jcf-select .jcf-select-opener {
      width: 40px;
      border: none; }
    .jcf-select-text {
      &:after {
        display: none; } } } }


.jcf-select-slct-sm {
  text-align: left;
  background-color: #fff;
  &.jcf-select {
    border-color: $border-catalog;
    height: 24px;
    border-radius: 3px;
    min-width: 113px;
    max-width: 150px; }
  &.jcf-drop-active {
    border-radius: 3px 3px 0 0; }
  &.jcf-drop-flipped.jcf-select {
    border-radius: 0 0 3px 3px; }
  &.jcf-select .jcf-select-text {
    color: $cl-site-text;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.43px;
    margin-left: 13px; }
  &.jcf-select .jcf-select-opener {
    width: 24px;
    border-left-color: $border-catalog;
    border-radius: 0px;
    &:before {
      content: '';
      font-family: $site-font-family;
      font-size: 0;
      line-height: 0;
      width: 10px;
      margin-left: -5px;
      height: 8px;
      margin-top: -4px;
      background: url(../img/icons/triangle-down.svg) center center no-repeat; } }
  .jcf-list .jcf-option {
    padding: 4px 13px; }
  .jcf-list {
    font-size: 12px;
    letter-spacing: 0.33px;
    font-weight: 300; }
  .jcf-select-drop .jcf-select-drop-content {
    border-color: $border-catalog; } }

/* end custom select */
