/* ----- Slider pirce ------------------- */

@import "standard/_noUiSlider.scss";

//slider-price
.noUi-target {
  border: 1px solid $cl-alto;
  border-left: none;
  border-right: none;
  box-shadow: none;
  background: #fff;
  border-radius: 0; }

.noUi-horizontal {
  height: 5px;
  .noUi-handle {
    width: 12px;
    height: 7px;
    top: -1px;
    left: -6px;
    outline: none;
    box-shadow: none;
    background-image: $gradient-st;
    border-radius: 2px;
    border: none;
    &:before, &:after {
      display: none; } } }

.noUi-connect {
  box-shadow: none;
  background: #000; }

.noUi-tooltip {
  color: #000;
  border: 1px solid $cl-alto;
  height: 30px;
  line-height: 30px;
  padding: 0 8px; }

.slider-price {
  position: relative;
  max-width: 230px;
  padding-left: 5px;
  padding-right: 5px;
  margin: 15px auto 0;
  // &:before, &:after
  //   content: ' '
  //   position: absolute
  //   left: -5px
  //   top: 0
  //   height: 1px
  //   width: 5px
  //   border: none
  //   background-color: $cl-boulder
  //   box-shadow: none
  // &:after
  //   left: auto
 }  //   right: -5px

html:not([dir="rtl"]) {
  .s-range {
    .noUi-horizontal .noUi-handle {
      right: -6px; } }
  .slider-price-wrapp {
    .noUi-horizontal .noUi-handle {
      right: -5px; } } }

.slider-price-inputs {
  overflow: hidden;
  display: flex;
  justify-content: space-between;

  input {
    height: 16px;
    padding: 0;
    color: #000;
    width: 51px;
    text-align: right;
    border: none;
    border-radius: 3px;
    color: $cl-site-text-three;
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    background-color: transparent;
    letter-spacing: $letspacing-standart; }
  > div {
    &:after {
      content: 'руб./м.п.';
      color: $cl-site-text-three;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: $letspacing-standart; } } }

.slider-price-input-one {
  margin-right: 30px;
  margin-left: -15px; }

.s-range {
  margin-bottom: 8px;
  &_last {
    margin-bottom: 0; }
  .noUi-target {
    padding-left: 6px;
    padding-right: 6px;
    max-width: 175px;
    margin-top: 8px; }
  .noUi-connects {
    margin-left: -6px;
    margin-right: -6px;
    width: auto;
    margin-top: -1px;
    margin-bottom: -1px;
    height: 5px; }
  .noUi-connect {
    top: -1px;
    bottom: -1px;
    height: auto;
    border-radius: 2px 0 0 2px;
    background-color: #8b888a; }
  .noUi-target {
    border-left: 1px solid $cl-alto;
    border-radius: 2px;
    border-right: 1px solid $cl-alto; } }

.s-range-val_width {
  &:before {
    content: 'Ширина окна: '; }
  &:after {
    content: ' м.'; } }

.s-range-val_height {
  &:before {
    content: 'Высота окна: '; }
  &:after {
    content: ' м.'; } }

.slider-price-wrapp {
  max-width: 300px;
  margin: 0 auto 10px;
  &_second {
    max-width: 232px;
    .slider-price-inputs input {
      font-size: 12px;
      font-family: $site-font-family-2;
      font-weight: 500; }
    .slider-price-inputs > div:after {
      font-size: 12px;
      font-family: $site-font-family-2;
      font-weight: 500; }
    .slider-price-input-one {
      margin-right: 5px; }
    .slider-price {
      margin-top: 26px;
      margin-bottom: 30px; } }
  .noUi-horizontal {
    height: 1px;
    background-color: $cl-boulder; }
  .noUi-target {
    border: none; }
  .noUi-handle {
    top: -5px;
    width: 11px;
    height: 11px;
    border-radius: 50%; } }

/* ----- END Slider pirce --------------- */
