/* ----- Step 1 edits ------------------- */

.banner-slider {
  .banner-slider-item {
    &:nth-child(n+1) {
      display: none; }
    &.slick-slide {
      display: block; } } }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-price {
    min-height: 555px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .header {
    .menu-item {
      margin-left: 9px; }
    .head-soc {
      margin-top: 95px; } }
  /* end media min width 1200 pixels */ }

/* ----- END Step 1 edits --------------- */
